import CachetaSkin from "../conf/skin/CachetaSkin";
import CachetaPlayer from "../ui/CachetaPlayer";
import CachetaUserMgr from "../manager/CachetaUserMgr";
import { CachetaConst } from "../define/CachetaConst";
import { CachetaProto } from "../service/ws/proto/CachetaProto";
import CachetaLogicMgr from "../manager/CachetaLogicMgr";

const { ccclass, property } = cc._decorator;

@ccclass
export default class CachetaPlayerSeat extends cc.Component {
    private players: CachetaPlayer[] = null;

    protected onLoad(): void {
        this.players = new Array(CachetaConst.PlayerNumMax);
        let prefab: cc.Prefab = UIDepend.PrefabManager.getPrefabBundle(
            CachetaSkin.Priority.CachetaPlayer
        );
        for (let i = 0; i < this.players.length; i++) {
            let node = cc.instantiate(prefab);
            node.parent = this.node.getChildByName("player_" + i);

            this.players[i] = node.getComponent(CachetaPlayer);
            this.players[i].init(i);
        }
    }

    public clear(): void {
        for (let i = 0; i < this.players.length; i++) {
            let player: CachetaPlayer = this.players[i];
            player.clear();
        }
    }

    public reset(): void {
        for (let i = 0; i < this.players.length; i++) {
            let player: CachetaPlayer = this.players[i];
            player.reset();
        }
    }

    /**
     * 
     * @param chair
     */
    public getPlayer(chair: number): CachetaPlayer {
        let seatId = CachetaUserMgr.getSeat(chair);
        if (this.players[seatId]) {
            return this.players[seatId];
        }
        return this.players[0];
    }

    public stopTimerAll(): void {
        for (let i = 0; i < this.players.length; i++) {
            this.players[i].stopTimer();
        }
    }

    /**
     * 
     */
    public getOtherPlayerPosList(): cc.Vec3[] {
        let posList: cc.Vec3[] = [];
        for (let i = 1; i < this.players.length; i++) {
            if (
                CachetaUserMgr.getStatus(CachetaUserMgr.getChair(i)) == CachetaProto.STATUS.U_PLAY
            ) {
                let pos = this.players[i].node.parent.position;
                posList.push(this.node.convertToWorldSpaceAR(pos));
            }
        }
        return posList;
    }

    /**
     * 
     */
    public getOtherPlayerHandPosList(): cc.Vec3[] {
        let posList: cc.Vec3[] = [];
        for (let i = 1; i < this.players.length; i++) {
            if (
                CachetaUserMgr.getStatus(CachetaUserMgr.getChair(i)) == CachetaProto.STATUS.U_PLAY
            ) {
                posList.push(this.players[i].getHandWorldPos());
            }
        }
        return posList;
    }

    /**
     * 
     */
    public showOtherPlayerHand(): void {
        for (let i = 1; i < this.players.length; i++) {
            let user = CachetaUserMgr.getUserByChair(CachetaUserMgr.getChair(i));
            if (user && user.Status == CachetaProto.STATUS.U_PLAY) {
                this.players[i].showHand();
            }
        }
    }

    /**
     * 
     * @param chair
     * @param robChair
     * @param callback
     */
    public playRobAction(chair: number, robChair: number, callback): void {
        this.scheduleOnce(() => {
            this.getPlayer(chair).playRobAction(this.getPlayer(robChair), () => {
                this.getPlayer(robChair).playRobedAction(callback);
            });
        }, 0.5 / CachetaLogicMgr.timeScale);
    }
}
