import CachetaLanguage from "../conf/lang/CachetaLanguage";
import CachetaAudioMgr from "../manager/CachetaAudioMgr";
import CachetaSocketMgr from "../service/ws/CachetaSocketMgr";
import CachetaUIMgr from "../manager/CachetaUIMgr";
import { CachetaEventName } from "../define/CachetaEventName";
import { CachetaProto } from "../service/ws/proto/CachetaProto";
import Listener from "../../../../../main/core/utils/Listener";
import CachetaLogicMgr from "../manager/CachetaLogicMgr";
import CachetaUserMgr from "../manager/CachetaUserMgr";
import { Tools } from "../../../../../main/core/utils/Tools";
import CachetaSkin from "../conf/skin/CachetaSkin";
import UIMgr from "../../../../plaza/common/Script/Component/base/UIMgr";
import CommonVersusLanguage from "../../../../plaza/common_versus/Script/conf/lang/CommonVersusLanguage";

const { ccclass, property } = cc._decorator;

@ccclass
export default class CachetaOperation extends cc.Component {
    private trust: cc.Node = null;
    private watching: cc.Node = null;
    private btnChangeTable: cc.Node = null;
    private btnDisCard: cc.Node = null;
    private btnTouchCard: cc.Node = null;
    private btnGetCard: cc.Node = null;
    private btnSort: cc.Node = null;
    private togAutoSort: cc.Node = null;
    private btnDraw: cc.Node = null;
    private btnDrawSp: cc.Sprite = null;
    private btnDrawB: cc.Button = null;
    private btnDeclare: cc.Node = null;
    private btnDeclareSp: cc.Sprite = null;
    private btnDeclareB: cc.Button = null;
    private drawLightSke: sp.Skeleton = null;
    private decLightSke: sp.Skeleton = null;

    protected onLoad(): void {
        this.trust = cc.find("trust", this.node);
        this.watching = cc.find("watching", this.node);
        cc.find("tip2", this.watching).getComponent(cc.Label).string =
            CommonVersusLanguage.waitingGameStart;
        cc.find("btnChangeTable", this.watching).on("click", this.onClickChangeTable, this);

        this.btnChangeTable = cc.find("btnChangeTable", this.node);
        this.btnChangeTable.on("click", this.onClickChangeTable, this);

        this.btnDisCard = cc.find("btnDisCard", this.node);
        this.btnDisCard.on("click", this.onClickDisCard, this);

        this.btnTouchCard = cc.find("btnTouchCard", this.node);
        this.btnTouchCard.on("click", this.onClickTouchCard, this);

        this.btnGetCard = cc.find("btnGetCard", this.node);
        this.btnGetCard.on("click", this.onClickGetOpenCard, this);

        this.btnSort = cc.find("btnSort", this.node);
        this.btnSort.on("click", this.onClickSort, this);

        this.togAutoSort = cc.find("togAutoSort", this.btnSort);
        this.togAutoSort.on("toggle", this.onClickAutoSort, this);

        this.btnDraw = cc.find("btnDraw", this.node);
        this.btnDraw.on("click", this.onClickDraw, this);
        this.btnDrawSp = this.btnDraw.getComponent(cc.Sprite);
        this.btnDrawB = this.btnDraw.getComponent(cc.Button);
        this.drawLightSke = cc.find("lightSke", this.btnDraw).getComponent(sp.Skeleton);

        this.btnDeclare = cc.find("btnDeclare", this.node);
        this.btnDeclare.on("click", this.onClickHu, this);
        this.btnDeclareSp = this.btnDeclare.getComponent(cc.Sprite);
        this.btnDeclareB = this.btnDeclare.getComponent(cc.Button);
        this.decLightSke = cc.find("lightSke", this.btnDeclare).getComponent(sp.Skeleton);

        this.setAutoSortState();
        this.reset();

        Listener.on(CachetaEventName.POKER_STATE_CHANGED, this.onPokerStateChanged, this);
    }

    protected onDestroy(): void {
        Listener.off(CachetaEventName.POKER_STATE_CHANGED, this.onPokerStateChanged, this);
    }

    public reset(): void {
        this.setTrustActive(false);
        this.setWatchingActive(false);
        this.setChangeTableActive(false);
        this.setDisCardActive(false);
        this.setTouchCardActive(false);
        this.setGetCardActive(false);
        this.setSortActive(false);
        this.setDrawActive(false);
        this.setDrawNoraml(false);
        this.setDeclareActive(false);
        this.setDeclareNoraml(false);
    }

    public setTrustActive(active: boolean): void {
        this.trust.active = active;
    }

    public setWatchingActive(active: boolean): void {
        this.watching.active = active;
    }

    public setChangeTableActive(active: boolean): void {
        this.btnChangeTable.active = active;
        this.btnChangeTable.x = 0;
    }

    public setDisCardActive(active: boolean): void {
        this.btnDisCard.active = active;
    }

    public setTouchCardActive(active: boolean): void {
        this.btnTouchCard.active = active;
    }

    public setGetCardActive(active: boolean): void {
        this.btnGetCard.active = active;
    }

    public setSortActive(active: boolean): void {
        this.btnSort.active = active;
    }

    public setDrawActive(active: boolean): void {
        this.btnDraw.active = active;
    }

    public setDrawNoraml(isNormal: boolean): void {
        this.btnDrawSp.spriteFrame = isNormal
            ? UIDepend.SpriteManager.getSpriteBundle(CachetaSkin.Priority.btnListenNormal)
            : UIDepend.SpriteManager.getSpriteBundle(CachetaSkin.Priority.btnListenGray);
        this.btnDrawB.interactable = isNormal;
        this.drawLightSke.node.active = isNormal;
    }

    public getDrawInteractable() {
        return this.btnDrawB.interactable;
    }

    public setDeclareActive(active: boolean): void {
        this.btnDeclare.active = active;
    }

    public setDeclareNoraml(isNormal: boolean): void {
        this.btnDeclareSp.spriteFrame = isNormal
            ? UIDepend.SpriteManager.getSpriteBundle(CachetaSkin.Priority.btnDeclareNormal)
            : UIDepend.SpriteManager.getSpriteBundle(CachetaSkin.Priority.btnDeclareGray);
        this.btnDeclareB.interactable = isNormal;
        this.decLightSke.node.active = isNormal;
    }

    public getDeclareInteractable() {
        return this.btnDeclareB.interactable;
    }

    private setAutoSortState(): void {
        this.togAutoSort.getComponent(cc.Toggle).isChecked = CachetaLogicMgr.autoSort;
    }

    private onClickChangeTable(event: cc.Event): void {
        Tools.clickManager(event.target);
        CachetaAudioMgr.playClickBtn();

        CachetaSocketMgr.changeTableReq();
    }

    /**
     * 
     */
    private onClickDisCard(event: cc.Event): void {
        Tools.clickManager(event.target);
        CachetaAudioMgr.playClickBtn();

        let pokers = CachetaUIMgr.handPoker.getSelectedPokers();
        if (pokers.length == 1) {
            let poker = pokers[0];
            CachetaUIMgr.handPoker.setDiscardPoker(poker);
            if (
                CachetaUserMgr.getLocalHandCard().Status == CachetaProto.HAND_STATUS.DECLARE &&
                pokers[0].getCardValue() == CachetaUIMgr.canDecCardV
            ) {
                CachetaSocketMgr.declareReq(poker.getCardValue(), poker.getCardIndex());
            } else {
                CachetaSocketMgr.discardReq(poker.getCardValue(), poker.getCardIndex());
            }
        } else {
            UIMgr.showToast(CachetaLanguage.DISCARD_TIP);
        }
    }

    /**
     * 
     */
    private onClickTouchCard(event: cc.Event): void {
        Tools.clickManager(event.target);
        CachetaAudioMgr.playClickBtn();

        CachetaSocketMgr.touchReq(false);
    }

    /**
     * 
     */
    private onClickGetOpenCard(event: cc.Event): void {
        Tools.clickManager(event.target);
        CachetaAudioMgr.playClickBtn();

        CachetaSocketMgr.touchReq(true);
    }

    private onClickSort(event: cc.Event): void {
        Tools.clickManager(event.target);
        CachetaAudioMgr.playClickBtn();

        Listener.emit(CachetaEventName.MANUAL_SORT);
    }

    private onClickAutoSort(event: cc.Event): void {
        Tools.clickManager(event.target);
        CachetaAudioMgr.playClickBtn();

        CachetaLogicMgr.autoSort = this.togAutoSort.getComponent(cc.Toggle).isChecked;
        if (CachetaLogicMgr.autoSort) {
        }
    }

    /**
     * 
     */
    private onClickDraw(event: cc.Event): void {
        Tools.clickManager(event.target);
        CachetaAudioMgr.playClickBtn();

        if (CachetaUserMgr.isLocalActive()) {
            let pokers = CachetaUIMgr.handPoker.getSelectedPokers();
            if (pokers.length == 1) {
                let poker = pokers[0];
                CachetaUIMgr.handPoker.setDiscardPoker(poker);
                CachetaSocketMgr.listenReq(poker.getCardValue(), poker.getCardIndex());
            } else {
                CachetaSocketMgr.listenReq();
            }
        }
    }

    /**
     * 
     */
    private onClickHu(event: cc.Event): void {
        Tools.clickManager(event.target);
        CachetaAudioMgr.playClickBtn();

        if (CachetaUserMgr.isLocalActive()) {
            let pokers = CachetaUIMgr.handPoker.getSelectedPokers();
            if (pokers.length == 1) {
                let poker = pokers[0];
                CachetaUIMgr.handPoker.setDiscardPoker(poker);
                CachetaSocketMgr.declareReq(poker.getCardValue(), poker.getCardIndex());
            } else {
                CachetaSocketMgr.declareReq();
            }
        }
    }

    private onPokerStateChanged(): void {
        if (
            CachetaUserMgr.isLocalActive() &&
            CachetaLogicMgr.gameStage == CachetaProto.STAGE.DISCARD
        ) {
            let pokers = CachetaUIMgr.handPoker.getSelectedPokers();
            if (pokers.length == 1) {
                CachetaSocketMgr.checkHandReq(pokers[0].getCardValue(), pokers[0].getCardIndex());
            }
        }
    }

    private onCheckHand(): void {
        //   
        if (
            CachetaUserMgr.isLocalActive() &&
            CachetaLogicMgr.gameStage == CachetaProto.STAGE.DISCARD
        ) {
            let pokers = CachetaUIMgr.handPoker.getSelectedPokers();
            if (pokers.length == 1) {
                CachetaSocketMgr.checkHandReq(pokers[0].getCardValue(), pokers[0].getCardIndex());
            }
        }
    }
}
