import BundleCenter from "../../../../../main/core/bd/BundleCenter";
import { BundleNames } from "../../../../../main/core/conf/BundleNames";
import { CachetaProto } from "../service/ws/proto/CachetaProto";
import CachetaSocketMgr from "../service/ws/CachetaSocketMgr";
import CachetaUIMgr from "./CachetaUIMgr";
import CachetaUserMgr from "./CachetaUserMgr";
import { CommonProto } from "../../../../plaza/common/Script/service/ws/proto/CommonProto";
import { CachetaProtoMap } from "../service/ws/proto/CachetaProtoMap";
import { lodash } from "../../../../../main/core/utils/NpmExport";
import GameReplayControl from "../../../../plaza/common/Script/game/replay/GameReplayControl";
import { NewGameInfo } from "../../../../plaza/common/Script/NewGameInfo";
import Listener from "../../../../../main/core/utils/Listener";
import { CachetaEventName } from "../define/CachetaEventName";

class CachetaLogicMgr {
    /**  */
    public autoSort = false;
    /**  */
    public autoExit = false;
    /**  */
    public conf: CachetaProto.Conf = null;
    /**  */
    public gameStage = CachetaProto.STAGE.WAIT;
    /**  */
    private wildCard: number[] = [];
    /**  */
    private pubCard: number = 0;
    /**  */
    private discardCards: number[] = [];
    /**  */
    private finishCard: number = 0;
    /**  */
    private closeNum: number = 0;
    /**  */
    private dropPool: number = 0;
    /**  */
    private lastSettleInfo: CachetaProto.SettleNt = null;
    private curDiscardCard: number = 0;

    /**  */
    private replayCtrl: GameReplayControl = null;

    public get isReplay(): boolean {
        return !lodash.isEmpty(NewGameInfo.getInstance().current.replayRecord);
    }
    public get timeScale(): number {
        return this.replayCtrl?.timeScale ?? 1;
    }

    get CurDiscardCard() {
        return this.curDiscardCard;
    }

    public init(): void {
        this.autoSort = false;
        this.autoExit = false;

        this.initServer();
    }

    public initServer(): void {
        if (!this.isReplay) {
            CachetaSocketMgr.init({
                onMessage: this.onMessage.bind(this),
                onError: this.onErrorSt.bind(this),
            });
        } else {
            this.replayCtrl = new GameReplayControl(
                NewGameInfo.getInstance().current.replayRecord,
                {
                    onMessage: this.onMessage.bind(this),
                    timeScale: 1,
                    messageMap: CachetaProtoMap,
                    messageDelay: {
                        [CachetaProto.MessageID.USER_IN_EV]: 0,
                        [CachetaProto.MessageID.PREPARE_NT]: 0,
                        [CachetaProto.MessageID.PREPARE_CANCEL_NT]: 0,
                    },
                }
            );
            this.replayCtrl.listenState((state) => {
                Listener.emit(CachetaEventName.REPLAY_STATE_CHANGED, state);
            }, this);

            CachetaUIMgr.createReplayCtrl(this.replayCtrl);
        }
    }

    private reset(): void {
        this.gameStage = CachetaProto.STAGE.WAIT;
        this.wildCard = [];
        this.discardCards = [];
        this.finishCard = 0;
        this.closeNum = 0;
        this.dropPool = 0;
        this.lastSettleInfo = null;

        CachetaUserMgr.reset();
    }

    public getWildCard(): number[] {
        return this.wildCard;
    }

    public isWildCard(cardValue: number): boolean {
        let find = false;

        this.wildCard.forEach((value) => {
            if (value == cardValue) {
                find = true;
            }
        });
        return find;
    }

    public getOpenCards(): number[] {
        return this.discardCards;
    }

    public getOpenCardsFirst(): number {
        return this.discardCards[0] || 0;
    }

    public getFinishCard(): number {
        return this.finishCard;
    }

    public getCloseNum(): number {
        return this.closeNum;
    }

    public getDropPool(): number {
        return this.dropPool;
    }

    public getPubCard(): number {
        return this.pubCard;
    }

    /**
     * rank
     */
    public getCardRank(cardValue: number): number {
        let rank = cardValue % 100;
        return rank;
    }

    /**
     * follow
     */
    public getfollow(cardValue: number): number {
        let follow = Math.floor(cardValue / 100); // hei, hong, mei, fang
        return follow;
    }

    /**
     * 
     */
    public getHandCardsNum(hand: CachetaProto.Hand) {
        let num = 0;
        for (let key in hand.GroupS) {
            num += hand.GroupS[key].CardS.length;
        }
        return num;
    }

    private setLastSettleInfo(msgData: CachetaProto.SettleNt | CachetaProto.RoomSt): void {
        this.lastSettleInfo = {
            TS: msgData.TS,
            NextTS: msgData.NextTS,
            SettleInfo: msgData.SettleInfo,
        } as CachetaProto.SettleNt;

        this.lastSettleInfo.NextTS =
            new Date().getTime() + this.lastSettleInfo.NextTS - this.lastSettleInfo.TS;
    }

    public getLastSettleInfo(): CachetaProto.SettleNt {
        return this.lastSettleInfo;
    }

    public exitGame(): void {
        if (CachetaUserMgr.getLocalStatus() == CachetaProto.STATUS.U_PLAY) {
            CachetaSocketMgr.userOutReq(CachetaUserMgr.getLocalChair());
        }
        cc.sys.localStorage.setItem("br-67", "0");
        BundleCenter.getInstance().launchScene(BundleNames.LOADING);
    }

    /**
     * socket 
     * @param message
     */
    private onMessage(message: ProtoObject<typeof CachetaProtoMap[CachetaProto.MessageID]>) {
        let funcName = `on${lodash.upperFirst(
            lodash.camelCase(CachetaProto.MessageID[message.msgId])
        )}`;
        this[funcName]?.(message.msgData);
        CachetaUIMgr[funcName]?.(message.msgData);
    }

    /**
     * 
     * @param error
     */
    private onErrorSt(error: CommonProto.ErrorST): void {
        CachetaUIMgr.onErrorSt(error);
    }

    /**
     * 
     * @param msgData
     */
    protected onUserSt(msgData: CachetaProto.UserSt): void {
        CachetaUserMgr.userIn(msgData);
    }

    /**
     * 
     * @param msgData
     */
    protected onRoomSt(msgData: CachetaProto.RoomSt): void {
        this.reset();

        this.conf = msgData.Conf;
        this.gameStage = msgData.Stage;
        this.wildCard = msgData.WildCardS;
        this.pubCard = msgData.PubCard;
        this.discardCards = msgData.OpenCardS;
        this.closeNum = msgData.CloseNum;

        CachetaUserMgr.setUsers(<any>msgData.UserM);
        CachetaUserMgr.setActiveChair(msgData.ActiveChair);

        if (msgData.SettleInfo) {
            this.setLastSettleInfo(msgData);
            CachetaUserMgr.winChair = msgData.SettleInfo.WinChair;
        }
    }

    /**
     * 
     * @param msgData
     */
    protected onUserInEv(msgData: CachetaProto.UserInEv): void {
        CachetaUserMgr.userIn(msgData.User);
    }

    /**
     * (/)
     * @param msgData
     */
    protected onUserOutEv(msgData: CachetaProto.UserOutEv): void {
        CachetaUserMgr.userOut(msgData.Chair);
    }

    /**
     * 
     * @param msgData
     */
    protected onPrepareNt(msgData: CachetaProto.PrepareNt): void {
        this.gameStage = CachetaProto.STAGE.PREPARE;
        CachetaUserMgr.setStatusAll(CachetaProto.STATUS.U_PREPARE);
    }

    /**
     * 
     * @param msgData
     */
    protected onPrepareCancelNt(msgData: CachetaProto.PrepareCancelNt): void {
        this.gameStage = CachetaProto.STAGE.WAIT;
    }

    /**
     * play
     * @param msgData
     */
    protected onPlayNt(msgData: CachetaProto.PlayNt): void {
        CachetaUserMgr.setActiveChair(msgData.Chair);
        if (this.gameStage != CachetaProto.STAGE.DISCARD) {
            this.gameStage = CachetaProto.STAGE.TOUCH;
        }
        CC_DEBUG && cc.log(`CachetaLogicMgr onPlayNt: ${CachetaProto.STAGE[this.gameStage]}`);
    }

    /**
     * close
     * @param msgData
     */
    protected onCloseOverNt(msgData: CachetaProto.CloseOverNt): void {
        this.closeNum = this.discardCards.length - 1;
        this.discardCards = [this.getOpenCardsFirst()];
    }

    /**
     * 
     * @param msgData
     */
    protected onTouchEv(msgData: CachetaProto.TouchEv): void {
        this.gameStage = CachetaProto.STAGE.DISCARD;
        if (msgData.Open) {
            this.curDiscardCard = this.discardCards.shift();
        } else {
            this.closeNum--;
        }

        if (CachetaUserMgr.isLocalChair(msgData.Chair)) {
            CachetaUserMgr.setLocalHandCard(msgData.Hand);
        }
    }

    /**
     * 
     * @param msgData
     */
    protected onDiscardEv(msgData: CachetaProto.DiscardEv): void {
        this.gameStage = CachetaProto.STAGE.TOUCH;
        this.discardCards.unshift(msgData.Card);

        if (CachetaUserMgr.isLocalChair(msgData.Chair)) {
            CachetaUserMgr.setLocalHandCard(msgData.Hand);
        }
    }

    /**
     * /
     * @param msgData
     */
    protected onListenEv(msgData: CachetaProto.ListenEv): void {
        msgData.Card && this.discardCards.unshift(msgData.Card);

        CachetaUserMgr.getUserByChair(msgData.Chair).InListen = true;
        if (CachetaUserMgr.isLocalChair(msgData.Chair)) {
            CachetaUserMgr.setLocalHandCard(msgData.Hand);
        }
    }

    /**
     * /
     */
    protected onDeclareEv(msgData: CachetaProto.DeclareEv): void {
        msgData.Card && this.discardCards.unshift(msgData.Card);

        if (CachetaUserMgr.isLocalChair(msgData.Chair)) {
            CachetaUserMgr.setLocalHandCard(msgData.Hand);
        }
    }

    /**
     * 
     * @param msgData
     */
    protected onCheckHandEv(msgData: CachetaProto.CheckHandEv): void {
        if (CachetaUserMgr.isLocalChair(msgData.Chair)) {
            CachetaUserMgr.setLocalHandCard(msgData.Hand);
        }
    }

    /**
     * 
     * @param msgData
     */
    protected onSettleNt(msgData: CachetaProto.SettleNt): void {
        this.gameStage = CachetaProto.STAGE.SETTLE;
        CachetaUserMgr.setStatusAll(CachetaProto.STATUS.U_PREPARE);
        CachetaUserMgr.winChair = msgData.SettleInfo.WinChair;

        for (let i = 0; i < msgData.SettleInfo.SettleS.length; i++) {
            let settle = msgData.SettleInfo.SettleS[i];
            CachetaUserMgr.setCredit(settle.Chair, settle.Credit);
        }

        this.setLastSettleInfo(msgData);
    }

    /**
     * 
     * @param msgData
     */
    protected onTrustNt(msgData: CachetaProto.TrustNt): void {
        CachetaUserMgr.getUserByChair(msgData.Chair).Trust = true;
    }

    /**
     * 
     * @param msgData
     */
    protected onCancelTrustEv(msgData: CachetaProto.CancelTrustEv): void {
        CachetaUserMgr.getUserByChair(msgData.Chair).Trust = false;
    }

    /**
     * 
     * @param msgData
     */
    protected onGroupEv(msgData: CachetaProto.GroupEv): void {
        CachetaUserMgr.setLocalHandCard(msgData.Hand);
    }
}

export default new CachetaLogicMgr();
