import { GameType } from "../../../../../../main/core/conf/GameType";
import BaseSocketMgr from "../../../../../plaza/common/Script/service/ws/BaseSocketMgr";
import { CachetaProto } from "./proto/CachetaProto";
import { CachetaProtoMap } from "./proto/CachetaProtoMap";
import { urlParser } from "../../../../../../main/core/utils/NpmExport";

class CachetaSocketMgr extends BaseSocketMgr<CachetaProto.MessageID> {
    protected gameId: number = GameType.CACHETA; //ws://localhost:5200
    protected host: string[] = this.isShf ? [this.shfHost || "wss://cachetax.tansuotv.net"] : null;
    protected isEncrypt: boolean = true;
    protected cryptId: number = 8000;
    public get isShf(): boolean {
        let shf = urlParser(window.location.href, true)?.query[`shf_${this.gameId}`];
        // return shf ? true : false;
        return true;
    }

    private get shfHost(): string {
        let shf = urlParser(window.location.href, true)?.query[`shf_${this.gameId}`];
        return "wss://cachetax.tansuotv.net";
    }

    public getMessageCtor<T extends CachetaProto.MessageID>(msgType: T) {
        return CachetaProtoMap[msgType];
    }

    public request<T extends CachetaProto.MessageID>(
        msgType: T,
        data: ProtoObject<typeof CachetaProtoMap[T]>
    ) {
        const ctor = this.getMessageCtor(msgType);
        this.send(msgType, ctor.fromObject(data));
    }

    public getConnectType() {
        if (this.reconnect) {
            return CachetaProto.ConnectType.CONNECT_NORMAL;
        }
        return CachetaProto.ConnectType.CONNECT_RE;
    }

    protected onAuthReq(): void {
        // let userId = JSON.parse(cc.sys.localStorage.getItem("userID"));
        let data = {
            TS: CommonDepend.CommonSocketModel.getCurrentTimestamp(),
            // UserID: userId,
            UserID: CommonDepend.UserManager.user_id,
            Token: CommonDepend.UserManager.token,
            RoomKind: CommonDepend.NewGameInfo.current.room,
            Version: CommonDepend.CommonSocketModel.getVersion(),
            CryptID: this.cryptId,
            BrandCode: CommonDepend.CommonSocketModel.getBrandCode(),
            game_id: this.gameId,
            connect_type: this.getConnectType(),
        };
        this.request(CachetaProto.MessageID.AUTH_REQ, data);
    }

    /**
     * 
     * @param chair
     */
    public userOutReq(chair: number): void {
        this.request(CachetaProto.MessageID.USER_OUT_REQ, {
            TS: new Date().getTime(),
            Chair: chair,
        });
    }

    /**
     * 
     */
    public syncUserReq(): void {
        this.request(CachetaProto.MessageID.SYNC_USER_REQ, {
            TS: new Date().getTime(),
        });
    }

    /**
     * 
     */
    public changeTableReq(): void {
        this.request(CachetaProto.MessageID.CHANGE_TABLE_REQ, {
            TS: new Date().getTime(),
        });
    }

    /**
     * 
     */
    public touchReq(open: boolean): void {
        this.request(CachetaProto.MessageID.TOUCH_REQ, {
            TS: new Date().getTime(),
            Open: open,
        });
    }

    /**
     * 
     * @param card 
     * @param index 
     */
    public discardReq(card: number, index: number): void {
        this.request(CachetaProto.MessageID.DISCARD_REQ, {
            TS: new Date().getTime(),
            Card: card,
            Index: index,
        });
    }

    /**
     * /
     */
    public listenReq(card: number = 0, index: number = -1): void {
        this.request(CachetaProto.MessageID.LISTEN_REQ, {
            TS: new Date().getTime(),
            Card: card,
            Index: index,
        });
    }

    /**
     * /
     */
    public declareReq(card: number = 0, index: number = -1): void {
        this.request(CachetaProto.MessageID.DECLARE_REQ, {
            TS: new Date().getTime(),
            Card: card,
            Index: index,
        });
    }

    /**
     * 
     */
    public checkHandReq(card: number, index: number): void {
        this.request(CachetaProto.MessageID.CHECK_HAND_REQ, {
            TS: new Date().getTime(),
            Card: card,
            Index: index,
        });
    }

    /**
     * 
     */
    public cancelTrustReq(): void {
        this.request(CachetaProto.MessageID.CANCEL_TRUST_REQ, {
            TS: new Date().getTime(),
        });
    }

    /**
     * 
     */
    public groupReq(hand: CachetaProto.Hand): void {
        this.request(CachetaProto.MessageID.GROUP_REQ, {
            TS: new Date().getTime(),
            Hand: hand,
        });
    }
}

export default new CachetaSocketMgr();
