import CachetaSkin from "../conf/skin/CachetaSkin";
import CachetaTable from "../ui/CachetaTable";
import CachetaMenu from "../ui/CachetaMenu";
import CachetaPlayerSeat from "../ui/CachetaPlayerSeat";
import CachetaDeck from "../ui/CachetaDeck";
import CachetaHandPoker from "../ui/CachetaHandPoker";
import CachetaOperation from "../ui/CachetaOperation";
import CachetaAction from "../ui/CachetaAction";
import CachetaToast from "../ui/CachetaToast";
import { CachetaProto } from "../service/ws/proto/CachetaProto";
import CachetaLanguage from "../conf/lang/CachetaLanguage";
import CachetaLogicMgr from "./CachetaLogicMgr";
import CachetaUserMgr from "./CachetaUserMgr";
import CachetaAudioMgr from "./CachetaAudioMgr";
import CachetaSocketMgr from "../service/ws/CachetaSocketMgr";
import UIMgr from "../../../../plaza/common/Script/Component/base/UIMgr";
import { CommonProto } from "../../../../plaza/common/Script/service/ws/proto/CommonProto";
import CommonSkin from "../../../../plaza/common/Script/conf/skin/CommonSkin";
import { GameType } from "../../../../../main/core/conf/GameType";
import StartAndStopAnim from "../../../../plaza/common/Script/Component/StartAndStopAnim";
import CachetaDeclare from "../ui/CachetaDeclare";
import CommonBroadCastUtil from "../../../../plaza/common/Script/broad_cast/CommonBroadCastUtil";
import { CachetaConst } from "../define/CachetaConst";
import Listener from "../../../../../main/core/utils/Listener";
import { CachetaEventName } from "../define/CachetaEventName";
import CommonVersusLanguage from "../../../../plaza/common_versus/Script/conf/lang/CommonVersusLanguage";
import GameReplayControl from "../../../../plaza/common/Script/game/replay/GameReplayControl";

class CachetaUIMgr {
    private root: cc.Node;
    private table: CachetaTable;
    private menu: CachetaMenu;
    private playerSeat: CachetaPlayerSeat;
    private deck: CachetaDeck;
    public handPoker: CachetaHandPoker;
    private operation: CachetaOperation;
    private action: CachetaAction;
    private toast: CachetaToast;
    private startAndEndController: StartAndStopAnim = null;
    private isRobSettle: boolean = false;
    private curTurnHandStatus: number = 0;
    public canDecCardV: number = 0;
    private maxRoundSettle: boolean = false;

    public init(node: cc.Node): void {
        this.root = node;

        this.initTable();
        this.initMenu();
        this.initPlayerSeat();
        this.initDeck();
        this.initHandPoker();
        this.initOperation();
        this.initAction();
        this.initToast();
        this.initStartEndAnim();

        let pos = cc.v3(
            cc.winSize.width / 2,
            cc.winSize.height / 2 + CachetaConst.broadCastOffsetY
        );
        CommonBroadCastUtil.setPostion(pos);
    }

    private reset(): void {
        this.isRobSettle = false;
        // this.maxRoundSettle = false;
        this.curTurnHandStatus = 0;
        this.root.stopAllActions();

        this.table.reset();
        this.playerSeat.reset();
        this.deck.reset();
        this.handPoker.reset();
        this.action.reset();
        this.operation.reset();
        this.toast.reset();

        this.removeSettle();
        this.removeDialog();
    }

    private initTable(): void {
        let prefab = UIDepend.PrefabManager.getPrefabBundle(CachetaSkin.Priority.CachetaTable);
        let node = cc.instantiate(prefab);
        node.parent = this.root;

        this.table = node.getComponent(CachetaTable);
    }

    private initMenu(): void {
        let prefab = UIDepend.PrefabManager.getPrefabBundle(CachetaSkin.Priority.CachetaMenu);
        let node = cc.instantiate(prefab);
        node.parent = this.root;

        this.menu = node.getComponent(CachetaMenu);
    }

    private initPlayerSeat(): void {
        let prefab = UIDepend.PrefabManager.getPrefabBundle(CachetaSkin.Priority.CachetaPlayerSeat);
        let node = cc.instantiate(prefab);
        node.parent = this.root;

        this.playerSeat = node.getComponent(CachetaPlayerSeat);
    }

    private initDeck(): void {
        let prefab = UIDepend.PrefabManager.getPrefabBundle(CachetaSkin.Priority.CachetaDeck);
        let node = cc.instantiate(prefab);
        node.parent = this.root;

        this.deck = node.getComponent(CachetaDeck);
    }

    private initHandPoker(): void {
        let prefab = UIDepend.PrefabManager.getPrefabBundle(CachetaSkin.Priority.CachetaHandPoker);
        let node = cc.instantiate(prefab);
        node.parent = this.root;

        this.handPoker = node.getComponent(CachetaHandPoker);
    }

    private initOperation(): void {
        let prefab = UIDepend.PrefabManager.getPrefabBundle(CachetaSkin.Priority.CachetaOperation);
        let node = cc.instantiate(prefab);
        node.parent = this.root;

        this.operation = node.getComponent(CachetaOperation);
    }

    private initAction(): void {
        let prefab = UIDepend.PrefabManager.getPrefabBundle(CachetaSkin.Priority.CachetaAction);
        let node = cc.instantiate(prefab);
        node.parent = this.root;

        this.action = node.getComponent(CachetaAction);
    }

    private initToast(): void {
        let prefab = UIDepend.PrefabManager.getPrefabBundle(CachetaSkin.Priority.CachetaToast);
        let node = cc.instantiate(prefab);
        node.parent = this.root;

        this.toast = node.getComponent(CachetaToast);
        node.setPosition(cc.v3(0, -50, 0));
    }

    private initStartEndAnim(): void {
        UIDepend.PrefabManager.loadPrefabInsBundle(
            CommonSkin.Priority.StartAndStopAnim,
            (prefab) => {
                let startAndEndFrame = cc.instantiate(prefab);
                if (cc.isValid(this.root)) {
                    this.root.addChild(startAndEndFrame);
                }
                if (startAndEndFrame.getComponent(StartAndStopAnim)) {
                    this.startAndEndController = startAndEndFrame.getComponent(StartAndStopAnim);
                    this.startAndEndController.init();
                }
            }
        );
    }

    public createSettle(settles: CachetaProto.Settle[], closeTime: number = 0): void {
        UIMgr.showUI(CachetaSkin.UnPriority.CachetaSettle, {
            args: [settles, closeTime],
            zIndex: UIMgr.UILayer.ALERT,
        });
    }

    public removeSettle(): void {
        UIMgr.close(CachetaSkin.UnPriority.CachetaSettle);
    }

    public createRule(): void {
        UIMgr.showUI(CommonSkin.LoadOnDemand.Rules[GameType.CACHETA], {
            args: [GameType.CACHETA],
            zIndex: UIMgr.UILayer.ALERT,
        });
    }

    public removeRule(): void {
        UIMgr.close(CommonSkin.LoadOnDemand.Rule);
    }

    public createSetting(): void {
        UIMgr.showUI(CommonSkin.LoadOnDemand.CommonUserSetting, {
            zIndex: UIMgr.UILayer.ALERT,
        });
    }

    public removeSetting(): void {
        UIMgr.close(CommonSkin.LoadOnDemand.CommonUserSetting);
    }

    public createDialog(option: IAlertParm, closeTime: number = -1): void {
        UIMgr.showAlert(option);
    }

    public removeDialog(): void {}

    public createReplayCtrl(ctrl: GameReplayControl): void {
        UIMgr.showUI(CommonSkin.LoadOnDemand.CommonGameReplay, {
            args: [ctrl],
            zIndex: cc.macro.MAX_ZINDEX - 1,
        });
    }

    public onErrorSt(error: CommonProto.ErrorST): void {
        switch (error.Status) {
            // 
            case CommonProto.STATUS.INSUFFICIENT:
                CachetaSocketMgr.close();
                this.playerSeat.clear();
                UIMgr.fightInsBalanceAlert(() => {
                    CachetaLogicMgr.exitGame();
                });
                break;
            // 
            case CommonProto.STATUS.IDLE_TIMEOUT:
                CachetaSocketMgr.close();
                this.playerSeat.clear();
                UIMgr.fightTimeOutAlert(
                    () => {
                        CachetaLogicMgr.exitGame();
                    },
                    () => {
                        CachetaLogicMgr.initServer();
                    }
                );
                break;
            default:
                break;
        }
    }

    protected onUserSt(msgData: CachetaProto.UserSt): void {
        this.playerSeat.getPlayer(msgData.Chair).userIn(msgData);
    }

    protected onRoomSt(msgData: CachetaProto.RoomSt): void {
        this.reset();
        this.maxRoundSettle = false;

        this.table.setBaseScore(CachetaLogicMgr.conf.Base);
        this.playerSeat.clear();

        for (let chair in msgData.UserM) {
            let user: CachetaProto.UserSt = msgData.UserM[chair];
            this.playerSeat.getPlayer(user.Chair).userIn(user);
        }

        if (CachetaUserMgr.getLocalStatus() != CachetaProto.STATUS.U_PLAY) {
            this.operation.setChangeTableActive(true);
        }

        if (CachetaLogicMgr.gameStage >= CachetaProto.STAGE.TOUCH) {
            this.deck.show();
            this.deck.setWildPoker(CachetaLogicMgr.getWildCard());
            this.deck.setPubPoker(CachetaLogicMgr.getPubCard());
            this.deck.setOpenPoker(CachetaLogicMgr.getOpenCardsFirst());
            this.deck.setClosePokerNum(CachetaLogicMgr.getCloseNum());

            if (CachetaUserMgr.getLocalStatus() == CachetaProto.STATUS.U_PLAY) {
                let handCard = CachetaUserMgr.getLocalHandCard();
                this.handPoker.setPokerValue(handCard, true);

                this.operation.setSortActive(true);
                if (!CachetaUserMgr.getLocalUser().InListen) {
                    this.operation.setDrawActive(true);
                } else {
                    this.operation.setDeclareActive(true);
                }
            } else if (CachetaUserMgr.getLocalStatus() == CachetaProto.STATUS.U_WATCH) {
                this.operation.setWatchingActive(true);
                this.operation.setChangeTableActive(false);
            }

            if (CachetaUserMgr.getLocalUser().Trust) {
                this.operation.setTrustActive(true);
                this.action.setBlockActive(true);
            }

            // 
            this.playerSeat.showOtherPlayerHand();
        }

        switch (CachetaLogicMgr.gameStage) {
            case CachetaProto.STAGE.WAIT:
                this.onStageWait(msgData);
                break;
            case CachetaProto.STAGE.PREPARE:
                this.onStagePrepare(msgData);
                break;
            case CachetaProto.STAGE.SHUFFLE:
                this.onStageShuffle(msgData);
                break;
            case CachetaProto.STAGE.TOUCH:
            case CachetaProto.STAGE.DISCARD:
                this.onStagePlay(msgData);
                break;
            case CachetaProto.STAGE.SETTLE:
                this.onStageSettle(msgData);
                break;
            default:
                break;
        }
    }

    protected onStageWait(msgData: CachetaProto.RoomSt): void {
        this.operation.setChangeTableActive(true);
        // this.toast.setSystemTips(CachetaLanguage.TIPS_GAME_START_WAITING);
        UIMgr.showToast(CommonVersusLanguage.waitingPalyer);
    }

    protected onStagePrepare(msgData: CachetaProto.RoomSt): void {
        this.operation.setChangeTableActive(true);
        this.toast.setSystemTips(
            CachetaLanguage.TIPS_GAME_START_TIME,
            (msgData.NextTS - msgData.TS) / 1000
        );
    }

    protected onStageShuffle(msgData: CachetaProto.RoomSt): void {
        let handCard = CachetaUserMgr.getLocalHandCard();
        let complete = (ani: boolean) => {
            if (!ani) {
                this.deck.show();
                this.deck.setWildPoker(CachetaLogicMgr.getWildCard());
                this.deck.setPubPoker(CachetaLogicMgr.getPubCard());
                // 
                this.playerSeat.showOtherPlayerHand();
            }

            if (handCard) {
                this.handPoker.setPokerValue(handCard, true);
            }
            this.operation.setSortActive(true);
            this.operation.setDrawActive(true);
        };

        let leftTime = (msgData.NextTS - msgData.TS) / 1000;
        if (leftTime < 6) {
            complete(false);
        } else {
            this.deck.show();
            let startPos = this.deck.getClosePokerPos();
            let endPosList = this.playerSeat.getOtherPlayerHandPosList();

            // 
            this.startAndEndController.playStartGameAnimAction(
                () => {
                    if (!cc.isValid(this.root)) return;

                    this.action.playOtherDealAni(startPos, endPosList, () => {
                        // 
                        this.playerSeat.showOtherPlayerHand();
                    });
                    this.handPoker.playDealAni(handCard, startPos, () => {
                        this.deck.playEnsurePubPokerAni(CachetaLogicMgr.getPubCard(), () => {
                            this.deck.setClosePokerNum(CachetaLogicMgr.getCloseNum());
                            this.deck.playPubandWildPokerAni(
                                CachetaLogicMgr.getPubCard(),
                                CachetaLogicMgr.getWildCard(),
                                () => {
                                    complete(true);
                                }
                            );
                        });
                    });
                },
                { timeScale: CachetaLogicMgr.timeScale }
            );
        }
    }

    protected onStagePlay(msgData: CachetaProto.RoomSt): void {
        this.playerSeat
            .getPlayer(msgData.ActiveChair)
            .startTimer(msgData.NextTS - msgData.TS, CachetaLogicMgr.conf.PlayDelay);

        if (CachetaUserMgr.getLocalStatus() == CachetaProto.STATUS.U_PLAY) {
            if (CachetaUserMgr.isLocalActive()) {
                CachetaAudioMgr.playOperation();

                if (CachetaLogicMgr.gameStage == CachetaProto.STAGE.TOUCH) {
                    this.operation.setTouchCardActive;
                    this.operation.setTouchCardActive(true);
                    this.operation.setGetCardActive(CachetaLogicMgr.getOpenCards().length > 0);
                    this.deck.setTouchTipActive(true, CachetaLogicMgr.getOpenCards().length == 0);
                } else {
                    this.operation.setDisCardActive(true);
                    this.canDecCardV = 0;
                }
            }
        }
    }

    protected onStageSettle(msgData: CachetaProto.RoomSt): void {
        this.reset();

        this.operation.setChangeTableActive(true);
        this.toast.setSystemTips(
            CommonVersusLanguage.waitingPalyer,
            (msgData.NextTS - msgData.TS) / 1000
        );

        this.createSettle(msgData.SettleInfo.SettleS);
    }

    protected onUserInEv(msgData: CachetaProto.UserInEv): void {
        this.playerSeat.getPlayer(msgData.User.Chair).userIn(msgData.User, true);
    }

    protected onUserOutEv(msgData: CachetaProto.UserOutEv): void {
        this.playerSeat.getPlayer(msgData.Chair).userOut();
    }

    protected onPrepareNt(msgData: CachetaProto.PrepareNt): void {
        this.operation.setChangeTableActive(true);
        this.toast.setSystemTips(
            CachetaLanguage.TIPS_GAME_START_TIME,
            (msgData.NextTS - msgData.TS) / 1000
        );

        Listener.emit(CachetaEventName.START_PREPARE, (msgData.NextTS - msgData.TS) / 1000);
    }

    protected onPrepareCancelNt(msgData: CachetaProto.PrepareCancelNt): void {
        // this.toast.setSystemTips(CachetaLanguage.TIPS_GAME_START_WAITING);
        UIMgr.showToast(CommonVersusLanguage.waitingPalyer);
    }

    protected onPlayNt(msgData: CachetaProto.PlayNt): void {
        this.playerSeat.stopTimerAll();
        let countTime = msgData.NextTS - msgData.TS;
        //  
        this.playerSeat
            .getPlayer(msgData.Chair)
            .startTimer(countTime, CachetaLogicMgr.conf.PlayDelay);

        if (CachetaLogicMgr.gameStage == CachetaProto.STAGE.TOUCH) {
            this.playerSeat.getPlayer(msgData.Chair).playTurnStartAnim();

            if (CachetaUserMgr.isLocalChair(msgData.Chair)) {
                this.operation.setTouchCardActive(true);
                this.operation.setGetCardActive(CachetaLogicMgr.getOpenCards().length > 0);
                this.deck.setTouchTipActive(true, CachetaLogicMgr.getOpenCards().length == 0);
            }
        }

        if (CachetaUserMgr.isLocalChair(msgData.Chair)) {
            CachetaAudioMgr.playOperation();
            this.removeDialog();

            this.curTurnHandStatus = CachetaUserMgr.getLocalHandCard().Status;

            // 
            if (
                !CachetaUserMgr.getLocalUser().InListen &&
                (CachetaUserMgr.getLocalHandCard().Status == CachetaProto.HAND_STATUS.LISTEN ||
                    CachetaUserMgr.getLocalHandCard().Status == CachetaProto.HAND_STATUS.DECLARE)
            ) {
                this.operation.setDrawNoraml(true);
            } else if (!CachetaUserMgr.getLocalUser().InListen) {
                this.operation.setDrawNoraml(false);
            }

            // 
            if (CachetaUserMgr.getLocalHandCard().Status == CachetaProto.HAND_STATUS.DECLARE) {
                this.operation.setDeclareNoraml(true);
            } else {
                this.operation.setDeclareNoraml(false);
            }
        } else {
            if (this.operation.getDrawInteractable()) {
                this.operation.setDrawNoraml(false);
            }
            if (this.operation.getDeclareInteractable()) {
                this.operation.setDeclareNoraml(false);
            }
        }
    }

    protected onCloseOverNt(msgData: CachetaProto.CloseOverNt): void {}

    protected onTouchEv(msgData: CachetaProto.TouchEv): void {
        let endPos: cc.Vec3 = null;
        if (CachetaUserMgr.isLocalChair(msgData.Chair)) {
            endPos = this.handPoker.getTouchPokerPos();

            this.deck.setTouchTipActive(false);
            this.operation.setTouchCardActive(false);
            this.operation.setGetCardActive(false);
            this.operation.setDisCardActive(true);
            this.removeDialog();

            this.curTurnHandStatus = msgData.Hand.Status;
        } else {
            endPos = this.playerSeat.getPlayer(msgData.Chair).getHandWorldPos();
            this.curTurnHandStatus = 0;
        }

        let startPos = msgData.Open ? this.deck.getOpenPokerPos() : this.deck.getClosePokerPos();
        this.action.playTouchPokerAni(
            startPos,
            endPos,
            msgData.Open ? CachetaLogicMgr.CurDiscardCard : msgData.Card,
            CachetaUserMgr.isLocalChair(msgData.Chair),
            msgData.Open,
            () => {
                CachetaAudioMgr.playCardIn();
                if (CachetaUserMgr.isLocalChair(msgData.Chair)) {
                    this.handPoker.setTouchPokerValue(msgData.Card);
                    if (!CachetaUserMgr.getLocalUser().Trust) {
                        this.handPoker.setTouchEnabled(false);
                        cc.tween(this.root)
                            .delay(1 / CachetaLogicMgr.timeScale)
                            .call(() => {
                                this.handPoker.setTouchEnabled(true);
                                this.handPoker.setPokerValue(msgData.Hand, true);
                            })
                            .start();
                    }
                }
            }
        );

        this.deck.setOpenPoker(CachetaLogicMgr.getOpenCardsFirst());
        this.deck.setClosePokerNum(CachetaLogicMgr.getCloseNum());

        this.playerSeat.getPlayer(msgData.Chair).stopTimer();
    }

    protected onDiscardEv(msgData: CachetaProto.DiscardEv, callback = null): void {
        let startPos: cc.Vec3 = null;
        if (CachetaUserMgr.isLocalChair(msgData.Chair)) {
            // startPos 
            startPos = this.handPoker.getDiscardPokerPos();
            this.handPoker.setPokerValue(msgData.Hand, true);
            this.operation.setDisCardActive(false);
            this.removeDialog();
        } else {
            startPos = this.playerSeat.getPlayer(msgData.Chair).getHandWorldPos();
        }

        let endPos = this.deck.getOpenPokerPos();
        this.action.playDiscardPokerAni(
            startPos,
            endPos,
            msgData.Card,
            CachetaUserMgr.isLocalChair(msgData.Chair),
            () => {
                CachetaAudioMgr.playCardOut();
                this.deck.setOpenPoker(CachetaLogicMgr.getOpenCardsFirst());
                callback && callback();
            }
        );

        this.playerSeat.getPlayer(msgData.Chair).stopTimer();
    }

    /**
     * /
     */
    protected onListenEv(msgData: CachetaProto.ListenEv): void {
        this.playerSeat.getPlayer(msgData.Chair).showListen(true);
        if (msgData.Card) {
            let discardEv = {
                TS: msgData.TS,
                Chair: msgData.Chair,
                Card: msgData.Card,
                Hand: msgData.Hand,
            } as CachetaProto.DiscardEv;
            this.onDiscardEv(discardEv);
        }

        if (CachetaUserMgr.isLocalChair(msgData.Chair)) {
            this.operation.setDrawActive(false);
            this.operation.setDeclareActive(true);
            if (!msgData.Card) {
                // 
                if (CachetaUserMgr.getLocalHandCard().Status == CachetaProto.HAND_STATUS.DECLARE) {
                    this.operation.setDeclareNoraml(true);
                }
            }
        }
    }

    /**
     * /
     */
    protected onDeclareEv(msgData: CachetaProto.DeclareEv): void {
        let declareAnim = () => {
            this.playerSeat.getPlayer(msgData.Chair).playWinnerAnim();
        };

        if (msgData.RobChair >= 0) {
            // 
            this.playerSeat.playRobAction(msgData.Chair, msgData.RobChair, declareAnim);
            this.isRobSettle = true;
        } else {
            this.maxRoundSettle = msgData.MaxRoundSettle;
            if (msgData.Card) {
                let discardEv = {
                    TS: msgData.TS,
                    Chair: msgData.Chair,
                    Card: msgData.Card,
                    Hand: msgData.Hand,
                } as CachetaProto.DiscardEv;
                this.onDiscardEv(discardEv, declareAnim);
            } else {
                if (!msgData.MaxRoundSettle) {
                    declareAnim();
                }
            }
        }

        if (CachetaUserMgr.isLocalChair(msgData.Chair)) {
            this.operation.setDeclareActive(false);
        }
    }

    /**
     * 
     * @param msgData
     */
    protected onCheckHandEv(msgData: CachetaProto.CheckHandEv): void {
        if (
            !(
                CachetaUserMgr.isLocalActive() &&
                CachetaLogicMgr.gameStage == CachetaProto.STAGE.DISCARD
            )
        )
            return;

        let pokers = this.handPoker.getSelectedPokers();
        // 
        if (!CachetaUserMgr.getLocalUser().InListen) {
            if (
                CachetaUserMgr.getLocalHandCard().Status >= CachetaProto.HAND_STATUS.LISTEN &&
                pokers.length == 1 &&
                pokers[0].getCardValue() == msgData.Card
            ) {
                this.operation.setDrawNoraml(true);
            } else {
                //  
                if (
                    this.curTurnHandStatus >= CachetaProto.HAND_STATUS.LISTEN &&
                    this.handPoker.getSelectedPokers().length == 0
                ) {
                    this.operation.setDrawNoraml(true);
                } else {
                    this.operation.setDrawNoraml(false);
                }
            }
        } else {
            this.canDecCardV = msgData.Card;
            // 
            if (
                CachetaUserMgr.getLocalHandCard().Status == CachetaProto.HAND_STATUS.DECLARE &&
                pokers.length == 1 &&
                pokers[0].getCardValue() == msgData.Card
            ) {
                this.operation.setDeclareNoraml(true);
            } else {
                //  
                if (
                    this.curTurnHandStatus == CachetaProto.HAND_STATUS.DECLARE &&
                    this.handPoker.getSelectedPokers().length == 0
                ) {
                    this.operation.setDeclareNoraml(true);
                } else {
                    this.operation.setDeclareNoraml(false);
                }
            }
        }
    }

    protected onSettleNt(msgData: CachetaProto.SettleNt): void {
        let time = 2;
        if (this.isRobSettle) {
            time = 3 + 2;
        }

        cc.tween(this.root)
            .delay(time / CachetaLogicMgr.timeScale)
            .call(() => {
                if (!cc.isValid(this.root)) return;

                this.reset();
                let winSettle;
                for (let i = 0; i < msgData.SettleInfo.SettleS.length; i++) {
                    if (msgData.SettleInfo.SettleS[i].Chair == msgData.SettleInfo.WinChair) {
                        winSettle = msgData.SettleInfo.SettleS[i];
                        break;
                    }
                }
                UIDepend.PrefabManager.loadPrefabBundle(
                    CachetaSkin.UnPriority.CachetaDeclare,
                    (res) => {
                        if (res && cc.isValid(this.root)) {
                            let node = cc.instantiate(res);
                            this.root.addChild(node);

                            CachetaAudioMgr.playDeclare();

                            node.getComponent(CachetaDeclare).init(
                                this.maxRoundSettle,
                                winSettle,
                                () => {
                                    if (!cc.isValid(this.root)) return;

                                    // 
                                    for (let i = 0; i < msgData.SettleInfo.SettleS.length; i++) {
                                        let settle = msgData.SettleInfo.SettleS[i];
                                        this.playerSeat
                                            .getPlayer(settle.Chair)
                                            .setCoin(settle.Credit, settle.Win, true);
                                    }

                                    let delay = 1.3;
                                    cc.tween(this.root)
                                        .delay(delay / CachetaLogicMgr.timeScale)
                                        .call(() => {
                                            this.operation.setChangeTableActive(true);
                                            // this.toast.setSystemTips(
                                            //     CachetaLanguage.TIPS_GAME_START_TIME,
                                            //     (msgData.NextTS - msgData.TS - delay * 1000) / 1000
                                            // );

                                            this.createSettle(msgData.SettleInfo.SettleS);
                                            this.maxRoundSettle = false;
                                        })
                                        .start();
                                }
                            );
                        }
                    }
                );
            })
            .start();
    }

    /**
     * 
     * @param msgData
     */
    protected onTrustNt(msgData: CachetaProto.TrustNt): void {
        if (CachetaUserMgr.isLocalChair(msgData.Chair)) {
            this.operation.setTrustActive(true);
            this.action.setBlockActive(true);
            CachetaUserMgr.getLocalUser().Trust = true;
        }
        this.playerSeat.getPlayer(msgData.Chair).showTrustActive(true);
    }

    /**
     * 
     * @param msgData
     */
    protected onCancelTrustEv(msgData: CachetaProto.CancelTrustEv): void {
        if (CachetaUserMgr.isLocalChair(msgData.Chair)) {
            this.operation.setTrustActive(false);
            this.action.setBlockActive(false);
            CachetaUserMgr.getLocalUser().Trust = false;
        }
        this.playerSeat.getPlayer(msgData.Chair).showTrustActive(false);
    }

    protected onGroupEv(msgData: CachetaProto.GroupEv): void {
        CachetaAudioMgr.playCardSort();
        this.handPoker.setPokerValue(msgData.Hand);

        // 
        if (!CachetaUserMgr.getLocalUser().InListen) {
            if (msgData.Hand.Status == CachetaProto.HAND_STATUS.LISTEN) {
                this.operation.setDrawNoraml(true);
            } else {
                this.operation.setDrawNoraml(false);
            }
        } else {
            // 
            if (msgData.Hand.Status == CachetaProto.HAND_STATUS.DECLARE) {
                this.operation.setDeclareNoraml(true);
            } else {
                this.operation.setDeclareNoraml(false);
            }
        }
    }

    /**
     *  
     */
    public adaptOperation() {
        if (!CachetaUserMgr.isLocalActive()) return;

        if (!CachetaUserMgr.getLocalUser().InListen) {
            if (
                this.curTurnHandStatus >= CachetaProto.HAND_STATUS.LISTEN &&
                this.handPoker.getSelectedPokers().length == 0
            ) {
                this.operation.setDrawNoraml(true);
            } else {
                this.operation.setDrawNoraml(false);
            }
        } else {
            if (
                this.curTurnHandStatus == CachetaProto.HAND_STATUS.DECLARE &&
                this.handPoker.getSelectedPokers().length == 0
            ) {
                this.operation.setDeclareNoraml(true);
            } else {
                this.operation.setDeclareNoraml(false);
            }
        }
    }
}

export default new CachetaUIMgr();
