import { Tools } from "../../../../../main/core/utils/Tools";
import CommonLanguage from "../../../../plaza/common/Script/conf/lang/CommonLanguage";
// import WalletUtil from "../../../../plaza/wallet/Script/view/utils/WalletUtil";
import CachetaAudioMgr from "../manager/CachetaAudioMgr";
import CachetaLogicMgr from "../manager/CachetaLogicMgr";
import CachetaUIMgr from "../manager/CachetaUIMgr";
import CachetaUserMgr from "../manager/CachetaUserMgr";
import { CachetaProto } from "../service/ws/proto/CachetaProto";

const { ccclass, property } = cc._decorator;

@ccclass
export default class CachetaMenu extends cc.Component {
    private btnExit: cc.Node = null;
    private btnMore: cc.Node = null;
    private btnMoreSp: cc.Sprite = null;
    private morePanel: cc.Node = null;
    private moreBg: cc.Node = null;
    private btnRule: cc.Node = null;
    private btnSetting: cc.Node = null;
    // private btnStore: cc.Node = null;

    private moreFold: boolean = true;

    protected onLoad(): void {
        let leftTop = cc.find("leftTop", this.node);
        let rightTop = cc.find("rightTop", this.node);

        this.btnExit = cc.find("btnExit", leftTop);
        this.btnExit.on("click", this.onClickExit, this);

        this.morePanel = cc.find("more/panel", rightTop);
        this.morePanel.active = !this.moreFold;
        this.morePanel.on(cc.Node.EventType.TOUCH_END, this.onTouchMorePanel, this);

        this.moreBg = cc.find("more/mask/bg", rightTop);
        this.moreBg.y = 275;

        this.btnMore = cc.find("more/btnMore", rightTop);
        this.btnMoreSp = this.btnMore.getComponent(cc.Sprite);
        this.btnMore.on("click", this.onClickMore, this);

        this.btnRule = cc.find("btnRule", this.moreBg);
        this.btnRule.on("click", this.onClickRule, this);

        this.btnSetting = cc.find("btnSetting", this.moreBg);
        this.btnSetting.on("click", this.onClickSetting, this);

        // this.btnStore = cc.find("btnStore", rightTop);
        // this.btnStore.on("click", this.onClickStore, this);
    }

    private onClickExit(event: cc.Event): void {
        Tools.clickManager(event.target);
        CachetaAudioMgr.playClickBtn();

        if (CachetaUserMgr.getLocalStatus() == CachetaProto.STATUS.U_PLAY) {
            CachetaUIMgr.createDialog({
                // titleText: CommonLanguage.noticeTitle,
                contentText: CommonLanguage.gaming,
                positiveBtnText: CommonLanguage.continueExit,
                negativeBtnText: CommonLanguage.cancel,
                positiveCallFunc: () => {
                    CachetaLogicMgr.exitGame();
                },
            });
        } else {
            CachetaLogicMgr.exitGame();
        }
    }

    private onTouchMorePanel(event: cc.Event): void {
        if (this.moreFold) {
            return;
        }

        this.setMoreState();
    }

    private onClickMore(event: cc.Event): void {
        Tools.clickManager(event.target);

        this.setMoreState();
    }

    private setMoreState(): void {
        CachetaAudioMgr.playClickBtn();

        this.moreFold = !this.moreFold;
        this.morePanel.active = !this.moreFold;
        this.btnMoreSp.enabled = this.moreFold;
        this.btnMore.getChildByName("fold").active = !this.moreFold;

        this.moreBg.stopAllActions();
        cc.tween(this.moreBg)
            .to(0.2 / CachetaLogicMgr.timeScale, { y: this.moreFold ? 275 : 36 })
            .start();
    }

    private onClickRule(event: cc.Event): void {
        Tools.clickManager(event.target);
        CachetaAudioMgr.playClickBtn();

        CachetaUIMgr.createRule();
    }

    private onClickSetting(event: cc.Event): void {
        Tools.clickManager(event.target);
        CachetaAudioMgr.playClickBtn();

        CachetaUIMgr.createSetting();
    }

    private onClickStore(event: cc.Event): void {
        Tools.clickManager(event.target);
        CachetaAudioMgr.playClickBtn();
        // WalletUtil.toTopup();
    }
}
