import PopUpViewBase from "../../../../../main/core/popUpViewUtils/PopUpViewBase";
import CachetaSkin from "../conf/skin/CachetaSkin";
import CachetaSettlePlayer from "./CachetaSettlePlayer";
import CachetaLanguage from "../conf/lang/CachetaLanguage";
import { CachetaProto } from "../service/ws/proto/CachetaProto";
import CachetaLogicMgr from "../manager/CachetaLogicMgr";
import CachetaSocketMgr from "../service/ws/CachetaSocketMgr";
import { Tools } from "../../../../../main/core/utils/Tools";
import Listener from "../../../../../main/core/utils/Listener";
import { CachetaEventName } from "../define/CachetaEventName";

const { ccclass, property } = cc._decorator;

@ccclass
export default class CachetaSettle extends PopUpViewBase {
    private btnClose: cc.Node = null;
    private btnBack: cc.Node = null;
    private btnChangeTable: cc.Node = null;
    private playerList: cc.Node = null;
    private tips: cc.RichText = null;
    private leftTime: number = 0;

    protected onLoad(): void {
        this.btnClose = cc.find("frame/btnClose", this.node);
        this.btnClose.on("click", this.onClickClose, this);

        this.btnBack = cc.find("frame/btnBack", this.node);
        this.btnBack.on("click", this.onClickBack, this);

        this.btnChangeTable = cc.find("frame/btnChangeTable", this.node);
        this.btnChangeTable.on("click", this.onClickChangeTable, this);

        this.playerList = cc
            .find("frame/context/playerList", this.node)
            .getComponent(cc.ScrollView).content;
        this.tips = cc.find("frame/tips", this.node).getComponent(cc.RichText);

        Listener.listen(CachetaEventName.START_PREPARE, this.startUpdateText, this);
    }

    protected onDestroy(): void {
        Listener.listenOff(CachetaEventName.START_PREPARE, this.startUpdateText, this);
    }

    public init(settles: CachetaProto.Settle[], closeTime: number): void {
        for (let i = 0; i < settles.length; i++) {
            let prefab: cc.Prefab = UIDepend.PrefabManager.getPrefabBundle(
                CachetaSkin.Priority.CachetaSettlePlayer
            );
            let node = cc.instantiate(prefab);
            node.parent = this.playerList;
            let player = node.getComponent(CachetaSettlePlayer);
            player.init(settles[i]);
        }
    }

    private startUpdateText(closeTime) {
        this.leftTime = Math.floor(closeTime) + 1;
        this.unschedule(this.updateText);
        this.schedule(this.updateText, 1);
        this.updateText();
    }

    private updateText(): void {
        this.leftTime -= 1;
        if (this.leftTime < 1) {
            this.close();
            return;
        }

        this.tips.string = CachetaLanguage.SETTLE_NEXT.format(this.leftTime);
    }

    private onClickClose(event: cc.Event): void {
        Tools.clickManager(event.target);

        this.close();
    }

    private onClickBack(event: cc.Event): void {
        Tools.clickManager(event.target);

        CachetaLogicMgr.exitGame();
        this.destroy();
    }

    private onClickChangeTable(event: cc.Event): void {
        Tools.clickManager(event.target);

        CachetaSocketMgr.changeTableReq();
        this.close();
    }
}
