import CachetaAudioMgr from "../manager/CachetaAudioMgr";
import { CachetaConst } from "../define/CachetaConst";
import CachetaSkin from "../conf/skin/CachetaSkin";
import { CachetaProto } from "../service/ws/proto/CachetaProto";
import UserAvatarMgr from "../../../../plaza/common/Script/Component/UserAvatar/UserAvatarMgr";
import CachetaPoker from "./CachetaPoker";
import { CurrencyUtil } from "../../../../plaza/common/Script/utils/CurrencyUtil";
import UserAvatarModel from "../../../../plaza/common/Script/Component/UserAvatar/UserAvatarModel";
import UserAvatar from "../../../../plaza/common/Script/Component/UserAvatar/UserAvatar";
import CachetaUserMgr from "../manager/CachetaUserMgr";
import CachetaUserCounddownProgress from "./CachetaUserCounddownProgress";
import Listener from "../../../../../main/core/utils/Listener";
import { CachetaEventName } from "../define/CachetaEventName";
import CachetaLogicMgr from "../manager/CachetaLogicMgr";
import { ReplayState } from "../../../../plaza/common/Script/game/replay/GameReplayControl";

const { ccclass, property } = cc._decorator;

@ccclass
export default class CachetaPlayer extends cc.Component {
    private avatar: cc.Node;
    private coin: cc.Node;
    private coinValue: cc.Label;
    private nickname: cc.Node;
    private turnStartSke: sp.Skeleton;
    private listenSke: sp.Skeleton;
    private robSke: sp.Skeleton;
    private winSke: sp.Skeleton;
    private cannonball: cc.Node;
    private handN: cc.Node;
    private timer: cc.Node;
    private timerCountdown: CachetaUserCounddownProgress;
    private countcownSke: sp.Skeleton;
    private timerClock: cc.Label;
    private tagNode: cc.Node;
    private coinChange: cc.Node;
    private state: cc.Node;
    private trust: cc.Node;

    /**  */
    private seatId: number = 0;
    /**  */
    private allTime: number = 0;
    /**  */
    private leftTime: number = 0;
    /**  */
    private progressFlag: number = 0;
    /**  */
    private clockFlag: number = 0;

    protected onLoad(): void {
        this.avatar = cc.find("head/mask/avatar", this.node);
        this.coin = cc.find("coin", this.node);
        this.coinValue = cc.find("value", this.coin).getComponent(cc.Label);
        this.nickname = cc.find("nickname", this.node);
        this.turnStartSke = cc.find("anim/turnStartSke", this.node).getComponent(sp.Skeleton);
        this.listenSke = cc.find("anim/listenSke", this.node).getComponent(sp.Skeleton);
        this.robSke = cc.find("anim/robSke", this.node).getComponent(sp.Skeleton);
        this.cannonball = cc.find("cannonball", this.robSke.node);
        this.handN = cc.find("handN", this.node);
        this.timer = cc.find("timer", this.node);
        this.timerCountdown = cc
            .find("countdown", this.timer)
            .getComponent(CachetaUserCounddownProgress);
        this.countcownSke = cc.find("countdown/countcownSke", this.timer).getComponent(sp.Skeleton);
        this.timerClock = cc.find("clock/value", this.timer).getComponent(cc.Label);
        this.tagNode = cc.find("tag", this.node);
        this.coinChange = cc.find("coinChange", this.node);
        this.state = cc.find("state", this.node);
        this.trust = cc.find("trust", this.state);
        this.winSke = cc.find("winSke", this.node).getComponent(sp.Skeleton);

        this.countcownSke.timeScale = CachetaLogicMgr.timeScale;

        Listener.on(CachetaEventName.REPLAY_STATE_CHANGED, this.onReplayStateChanged, this);
    }

    protected onDestroy(): void {
        Listener.off(CachetaEventName.REPLAY_STATE_CHANGED, this.onReplayStateChanged, this);
    }

    public init(seatId: number): void {
        this.seatId = seatId;

        // 
        if (this.seatId == 2) {
            this.coin.setPosition(cc.v3(127, -24, 0));
            this.nickname.setPosition(cc.v3(127, 9, 0));
            // this.coinValue.node.color = cc.color().fromHEX("#a8450a");
        }

        if (this.seatId == 3 || this.seatId == 2) {
            this.handN.setPosition(cc.v3(-88, -5, 0)); // cc.v3(97, -5, 0)
            this.listenSke.node.x = -38;
        }
        this.tagNode.position = this.seatId == 0 ? cc.v3(32, 60) : cc.v3(38, 50);

        if (this.seatId == 0) {
            this.setAvatar(CommonDepend.UserManager.avatar, CommonDepend.UserManager.gender);
            this.setCoin(CommonDepend.UserManager.coin);
            this.setNickname(CommonDepend.UserManager.nick);
            this.playIn();
        } else {
            let pokerPrefab: cc.Prefab = UIDepend.PrefabManager.getPrefabBundle(
                CachetaSkin.Priority.CachetaPoker
            );

            let poker = cc.instantiate(pokerPrefab);
            poker.parent = this.handN;
            poker.setPosition(cc.v3(0, 0, 0));
            let pokerJS = poker.getComponent(CachetaPoker);
            pokerJS.showBack();
            pokerJS.setTouchEnabled(false);
        }

        this.clear();
        this.reset();
    }

    public clear(): void {
        if (this.seatId == 0) {
            return;
        }

        this.node.active = false;
    }

    public reset(): void {
        this.node.stopAllActions();
        let children = this.node.children;
        for (let i = 0; i < children.length; i++) {
            children[i].stopAllActions();
        }

        this.node.position = cc.v3(0, 0);
        this.node.opacity = 255;
        this.coinChange.active = false;
        this.turnStartSke.node.active = false;
        this.winSke.node.active = false;
        this.listenSke.node.active = false;
        this.robSke.node.active = false;
        this.robSke.node.scaleX = 1;
        this.cannonball.active = false;
        this.handN.active = false;
        this.showTrustActive(false);

        this.stopTimer();
        this.setTag(CachetaConst.PLAYER_TAG.Null);
    }

    /**
     * 
     * @param user
     */
    public userIn(user: CachetaProto.UserSt, ani: boolean = false): void {
        this.reset();
        this.node.active = true;

        this.setAvatar(user.Avatar, user.Gender);
        this.setCoin(user.Credit);
        this.setNickname(user.UserName);
        this.setTag(CachetaConst.PLAYER_TAG.Null);

        if (this.seatId == 0) {
            // 
            this.setAvatar(CommonDepend.UserManager.avatar, CommonDepend.UserManager.gender);
        }

        if (ani) {
            this.playIn(user.Status == CachetaProto.STATUS.U_WATCH);
        } else {
            if (user.Status == CachetaProto.STATUS.U_WATCH) {
                this.node.opacity = 255 / 2;
            }
        }

        if (CachetaLogicMgr.gameStage >= CachetaProto.STAGE.SHUFFLE && user.InListen) {
            this.showListen();
        }
        this.showTrustActive(user.Trust);
    }

    /**
     * 
     */
    public userOut(): void {
        this.playOut();
    }

    /**
     * 
     * @param avatar
     * @param gender
     */
    public setAvatar(avatar: number, gender: UserAvatarModel.GENDER): void {
        UserAvatarMgr.loadUserAvatarPrefab(this.avatar, avatar, gender).then((avatar: cc.Node) => {
            if (cc.isValid(avatar)) {
                avatar.position = cc.v3(0, 0);
                avatar.scaleX = (this.avatar.width + 15) / avatar.width;
                avatar.scaleY = (this.avatar.height + 15) / avatar.height;
            }
        });
    }

    private addition = 0;

    /**
     * 
     * @param amount
     * @param change
     * @param ani
     */
    public setCoin(amount: number, change: number = 0, ani: boolean = false): void {
        let base = cc.sys.localStorage.getItem("Base");
        if (change < 0) {
            if (base == 10) {
                change = -500 * 10;
            } else if (base == 50) {
                change = -500 * 50;
            }
        } else {
            if (base == 10) {
                change = 1500 * 10;
            } else if (base == 50) {
                change = 1500 * 50;
            }
        }
        this.coinValue.string = amount + "";

        if (change > 0) {
            let vip = cc.sys.localStorage.getItem("vip");
            if (vip != null) {
                if (vip == "1") {
                    this.addition = 0.01;
                } else if (vip == "2") {
                    this.addition = 0.03;
                } else if (vip == "3") {
                    this.addition = 0.05;
                } else if (vip == "4") {
                    this.addition = 0.08;
                } else if (vip == "5") {
                    this.addition = 0.1;
                }
            } else {
                this.addition = 0;
            }
        }
        let sum = this.addition * change + change;

        if (ani) {
            this.coinChange.active = true;
            let labAdd = cc.find("add", this.coinChange);
            labAdd.active = change >= 0;
            let labSub = cc.find("sub", this.coinChange);
            labSub.active = change < 0;

            let label: cc.Node = null;
            let strValue = Math.abs(sum) + "";
            if (change >= 0) {
                label = labAdd;
                strValue = "+" + strValue;
            } else {
                label = labSub;
                strValue = "-" + strValue;
            }

            label.active = true;
            label.opacity = 255;
            label.position = cc.v3(0, 0);
            label.getComponent(cc.Label).string = strValue;
            label.stopAllActions();

            cc.tween(label)
                .to(
                    0.5 / CachetaLogicMgr.timeScale,
                    { position: cc.v3(0, 100) },
                    { easing: cc.easing.quadIn.name }
                )
                .to(
                    0.5 / CachetaLogicMgr.timeScale,
                    { position: cc.v3(0, 60) },
                    { easing: cc.easing.quadOut.name }
                )
                .delay(1 / CachetaLogicMgr.timeScale)
                .to(1 / CachetaLogicMgr.timeScale, { opacity: 0 })
                .call(() => {
                    this.coinChange.active = false;
                    label.active = false;
                })
                .start();

            if (change >= 0) {
                this.playWin();
            } else {
                this.playLose();
            }
        }
    }

    /**
     * 
     */
    public setNickname(nickname: string): void {
        this.nickname.getChildByName("value").getComponent(cc.Label).string =
            nickname.limtLength(10);
    }

    /**
     * 
     * @param leftTime ms
     * @param allTime ms
     */
    public startTimer(leftTime: number, allTime: number): void {
        if (
            CachetaUserMgr.getStatus(CachetaUserMgr.getChair(this.seatId)) !=
            CachetaProto.STATUS.U_PLAY
        ) {
            return;
        }

        this.timerCountdown.play(allTime / 1000);

        leftTime /= 1000;
        allTime /= 1000;

        this.timer.active = true;
        // this.timerProgressBox.fillRange = 1;
        // this.timerProgressBg.fillRange = 1;
        // this.timerLight.angle = -360;
        this.timerClock.string = (Math.floor(leftTime) + 1).toString();
        this.allTime = allTime;
        this.leftTime = leftTime;
        this.progressFlag = 0;
        this.clockFlag = 0;
        // this.schedule(this.updateTimer, 0, cc.macro.REPEAT_FOREVER);
    }

    /**
     * 
     */
    public stopTimer(): void {
        this.timer.active = false;
        this.timerCountdown.stop();
        // this.unschedule(this.updateTimer);
    }

    /**
     * 
     * @param dt
     */
    private updateTimer(dt: number): void {
        if (this.timer.active) {
            this.leftTime -= dt * CachetaLogicMgr.timeScale;
            let progress = this.leftTime / this.allTime;
            if (progress >= 0.33) {
                if (this.progressFlag != 1) {
                    this.progressFlag = 1;
                    // this.timerProgressBg.spriteFrame = UIDepend.SpriteManager.getSpriteBundle(
                    //     CachetaSkin.Priority.timer_progress_bg_yellow
                    // );
                }
            } else {
                if (this.progressFlag != 2) {
                    this.progressFlag = 2;
                    // this.timerProgressBg.spriteFrame = UIDepend.SpriteManager.getSpriteBundle(
                    //     CachetaSkin.Priority.timer_progress_bg_red
                    // );
                }
            }

            // this.timerProgressBox.fillRange = progress;
            // this.timerProgressBg.fillRange = progress;
            // this.timerLight.angle = -360 * (1 - progress);
            this.timerClock.string = Math.floor(this.leftTime + 1).toString();

            if (this.seatId == 0) {
                // 5 
                if (this.leftTime <= 5 && Math.floor(this.leftTime) != this.clockFlag) {
                    this.clockFlag = Math.floor(this.leftTime);
                    CachetaAudioMgr.playCountdown();
                }
            }

            if (this.leftTime <= 0) {
                this.stopTimer();
            }
        }
    }

    /**
     * 
     * @param tag
     * @param score
     */
    public setTag(tag: CachetaConst.PLAYER_TAG, score: number = 0): void {
        let active = false;

        let bg = this.tagNode.getChildByName("bg").getComponent(cc.Sprite);
        let value = this.tagNode.getChildByName("value").getComponent(cc.Label);

        switch (tag) {
            case CachetaConst.PLAYER_TAG.Drop:
                active = true;
                bg.spriteFrame = UIDepend.SpriteManager.getSpriteBundle(
                    CachetaSkin.Priority.tag_bg_lost
                );
                // value.string = CachetaLanguage.STATUS_DROP;
                break;
            case CachetaConst.PLAYER_TAG.Win:
                active = true;
                bg.spriteFrame = UIDepend.SpriteManager.getSpriteBundle(
                    CachetaSkin.Priority.tag_bg_won
                );
                // value.string = CachetaLanguage.STATUS_WON;
                break;
            case CachetaConst.PLAYER_TAG.Lost:
                active = true;
                bg.spriteFrame = UIDepend.SpriteManager.getSpriteBundle(
                    CachetaSkin.Priority.tag_bg_lost
                );
                // value.string = CachetaLanguage.STATUS_LOST.format(score);
                break;
            default:
                break;
        }

        this.tagNode.active = active;
    }

    /**
     * 
     */
    public getPlayerPos(): cc.Vec3 {
        return this.node.convertToWorldSpaceAR(cc.v3(0, 0));
    }

    /**
     * 
     */
    public getHandWorldPos(): cc.Vec3 {
        return this.handN.convertToWorldSpaceAR(cc.v3(0, 0));
    }

    /**
     * 
     */
    private playIn(isWatch: boolean = false): void {
        let posX = 0;
        let posY = 0;
        let move = CachetaConst.HeadOffset;
        switch (this.seatId) {
            case 0:
                posY -= move / 2;
                break;
            case 1:
                posX -= move;
                break;
            case 2:
                posY += move / 2;
                break;
            case 3:
                posX += move;
                break;
            default:
                break;
        }

        this.node.position = cc.v3(posX, posY);
        this.node.active = true;
        this.node.opacity = 0;
        this.node.stopAllActions();

        cc.tween(this.node)
            .to(
                0.6 / CachetaLogicMgr.timeScale,
                { position: cc.v3(0, 0), opacity: isWatch ? 255 / 2 : 255 },
                { easing: cc.easing.quadOut.name }
            )
            .start();
    }

    /**
     * 
     */
    private playOut(): void {
        let posX = 0;
        let posY = 0;
        let move = CachetaConst.HeadOffset;
        switch (this.seatId) {
            case 0:
                posY -= move / 2;
                break;
            case 1:
                posX -= move;
                break;
            case 2:
                posY += move / 2;
                break;
            case 3:
                posX += move;
                break;
            default:
                break;
        }

        this.node.stopAllActions();

        cc.tween(this.node)
            .to(
                0.6 / CachetaLogicMgr.timeScale,
                { position: cc.v3(posX, posY), opacity: 0 },
                { easing: cc.easing.quadOut.name }
            )
            .call(() => {
                this.node.active = false;
                this.node.x = 0;
                this.node.y = 0;
            })
            .start();
    }

    private playWin(): void {
        let userAvatar = this.avatar.getComponentInChildren(UserAvatar);
        if (userAvatar) {
            userAvatar.playWinAnimation();
        }

        if (this.seatId == 0) {
            CachetaAudioMgr.playWin();
        }
    }

    private playLose(): void {
        let userAvatar = this.avatar.getComponentInChildren(UserAvatar);
        if (userAvatar) {
            userAvatar.playLoseAnimation();
        }

        if (this.seatId == 0) {
            CachetaAudioMgr.playLose();
        }
    }

    public showHand(): void {
        this.handN.active = true;
    }

    public showListen(isFirst: boolean = false): void {
        this.listenSke.node.active = true;
        this.listenSke.clearTracks();
        this.listenSke.timeScale = CachetaLogicMgr.timeScale;
        if (isFirst) {
            this.listenSke.setAnimation(0, "Appear", false);
            this.listenSke.setCompleteListener(() => {
                this.listenSke.setAnimation(0, "continued", true);
            });
        } else {
            this.listenSke.setAnimation(0, "continued", true);
        }
    }

    public showTrustActive(active: boolean): void {
        this.state.active = active;
        this.trust.active = active;
    }

    public playTurnStartAnim(): void {
        this.turnStartSke.node.active = true;
        this.turnStartSke.clearTracks();
        this.turnStartSke.timeScale = CachetaLogicMgr.timeScale;
        this.turnStartSke.setAnimation(0, "animation", false);
        this.turnStartSke.setCompleteListener(() => {
            this.turnStartSke.node.active = false;
        });
    }

    public playWinnerAnim(): void {
        this.winSke.node.active = true;
        this.winSke.clearTracks();
        this.winSke.timeScale = CachetaLogicMgr.timeScale;
        this.winSke.setAnimation(0, "animation", false);
        // this.winSke.setCompleteListener(() => {
        //     this.winSke.node.active = false;
        // });
    }

    public playRobAction(robPlayer: CachetaPlayer, callback): void {
        this.robSke.node.active = true;
        UIDepend.SpineManager.loadSkeletonBundle(
            this.robSke,
            CachetaSkin.UnPriority.CachetaFire,
            () => {
                if (!cc.isValid(this.node)) return;

                switch (this.seatId) {
                    case 0:
                        if (robPlayer.seatId == 1) {
                            this.robSke.node.scaleX = 1;
                        } else {
                            this.robSke.node.scaleX = -1;
                        }
                        break;
                    case 1:
                        this.robSke.node.scaleX = -1;
                        break;
                    case 2:
                        if (robPlayer.seatId == 3) {
                            this.robSke.node.scaleX = -1;
                        }
                        break;
                    case 3:
                        break;
                    default:
                        break;
                }
                // 
                let pPos = this.node.parent.convertToNodeSpaceAR(
                    robPlayer.node.convertToWorldSpaceAR(cc.v3(0, 0, 0))
                );
                let radian = Math.atan((this.node.x - pPos.x) / (this.node.y - pPos.y));
                let degree = (radian * 180) / 3.14;
                let angle = degree;
                angle = angle > 0 ? 90 - angle : -90 - angle;
                CC_DEBUG && cc.log(`CachetaPlayer playRobAction: angle: ${angle}`);
                this.robSke.node.angle = angle;

                this.robSke.clearTracks();
                this.robSke.timeScale = CachetaLogicMgr.timeScale;
                this.robSke.setAnimation(0, "animation", false);
                this.robSke.setCompleteListener(() => {
                    // this.robSke.node.active = false;
                    this.robSke.skeletonData = null;
                });
                Listener.emit(
                    CachetaEventName.ROB_FIRE,
                    this.cannonball.convertToWorldSpaceAR(cc.v3(0, 0, 0)),
                    robPlayer.node.convertToWorldSpaceAR(cc.v3(0, 0, 0)),
                    callback
                );
            }
        );
    }

    public playRobedAction(callback): void {
        this.robSke.node.active = true;
        UIDepend.SpineManager.loadSkeletonBundle(
            this.robSke,
            CachetaSkin.UnPriority.CachetaExplode,
            () => {
                if (!cc.isValid(this.node)) return;

                CachetaAudioMgr.playExplode();

                this.robSke.node.angle = 0;
                this.robSke.node.y = -10;
                this.robSke.clearTracks();
                this.robSke.timeScale = CachetaLogicMgr.timeScale;
                this.robSke.setAnimation(0, "animation", false);
                this.robSke.setCompleteListener(() => {
                    this.robSke.node.active = false;
                    this.robSke.skeletonData = null;

                    this.robSke.node.y = 0;
                    callback && callback();
                });
            }
        );
    }

    private onReplayStateChanged(state: ReplayState): void {
        switch (state) {
            case ReplayState.Play:
                this.listenSke.paused = false;
                this.countcownSke.paused = false;
                this.countcownSke.timeScale = CachetaLogicMgr.timeScale;
                break;
            case ReplayState.Pause:
                this.listenSke.paused = true;
                this.countcownSke.paused = true;
                this.countcownSke.timeScale = CachetaLogicMgr.timeScale;
                break;
            default:
                break;
        }
    }
}
