import CachetaPoker from "./CachetaPoker";
import CachetaSocketMgr from "../service/ws/CachetaSocketMgr";
import { CachetaProto } from "../service/ws/proto/CachetaProto";
import CachetaAudioMgr from "../manager/CachetaAudioMgr";
import CachetaSkin from "../conf/skin/CachetaSkin";
import CachetaLogicMgr from "../manager/CachetaLogicMgr";
import CachetaUserMgr from "../manager/CachetaUserMgr";
import Listener from "../../../../../main/core/utils/Listener";
import { CachetaEventName } from "../define/CachetaEventName";
import { ReplayState } from "../../../../plaza/common/Script/game/replay/GameReplayControl";

const { ccclass, property } = cc._decorator;

@ccclass
export default class CachetaDeck extends cc.Component {
    private closeBg: cc.Node = null;
    private oepnBg: cc.Node = null;
    private pubCacdN: cc.Node = null;
    private light: cc.Node = null;
    private pubCacdNull: cc.Node = null;
    private pubPokerBig: CachetaPoker = null;
    private wildShow: cc.Node = null;
    private wildShowNull1: cc.Node = null;
    private wildShowNull2: cc.Node = null;
    private pokerPanel: cc.Node = null;
    private pubPoker: CachetaPoker = null;
    private wildPoker1: CachetaPoker = null;
    private wildPoker2: CachetaPoker = null;
    private closePoker: CachetaPoker = null;
    private openPoker: CachetaPoker = null;
    private closeTips: cc.Node = null;
    private oepnTips: cc.Node = null;

    protected onLoad(): void {
        this.closeBg = cc.find("closeBg", this.node);
        this.oepnBg = cc.find("oepnBg", this.node);
        this.pubCacdN = cc.find("pubCacdBg", this.node);
        this.light = cc.find("light", this.pubCacdN);
        this.pubCacdNull = cc.find("null", this.pubCacdN);
        this.wildShow = cc.find("wildShow", this.node);
        this.wildShowNull1 = cc.find("null1", this.wildShow);
        this.wildShowNull2 = cc.find("null2", this.wildShow);

        this.pokerPanel = cc.find("pokerPanel", this.node);
        let pokerPrefab: cc.Prefab = UIDepend.PrefabManager.getPrefabBundle(
            CachetaSkin.Priority.CachetaPoker
        );

        this.pokerPanel.getChildByName("cardPile").zIndex = 1;
        this.pubPokerBig = this.getNewPoker(pokerPrefab, this.pubCacdN.getChildByName("pokerN"));
        this.pubPoker = this.getNewPoker(pokerPrefab, this.pokerPanel);
        this.wildPoker1 = this.getNewPoker(pokerPrefab, this.wildShow.getChildByName("wildCardP1"));
        this.wildPoker2 = this.getNewPoker(pokerPrefab, this.wildShow.getChildByName("wildCardP2"));
        this.pubPoker.node.zIndex = 0;

        this.closePoker = this.getNewPoker(pokerPrefab, this.pokerPanel);
        this.closePoker.node.on(cc.Node.EventType.TOUCH_END, this.onTouchClosePoker, this);

        this.openPoker = this.getNewPoker(pokerPrefab, this.pokerPanel);
        this.openPoker.node.on(cc.Node.EventType.TOUCH_END, this.onTouchOpenPoker, this);

        this.closeTips = cc.find("touchTips/closeTips", this.node);
        this.oepnTips = cc.find("touchTips/oepnTips", this.node);

        this.reset();

        Listener.on(CachetaEventName.REPLAY_STATE_CHANGED, this.onReplayStateChanged, this);
    }

    protected onDestroy(): void {
        Listener.off(CachetaEventName.REPLAY_STATE_CHANGED, this.onReplayStateChanged, this);
    }

    public reset(): void {
        this.wildPoker1.node.stopAllActions();
        this.wildPoker2.node.stopAllActions();

        this.pubPoker.node.stopAllActions();
        this.pubPoker.node.position = cc.v3(-81.2, 38, 0);
        this.pubPoker.node.scale = 0.59;
        this.pubPoker.node.zIndex = 0;

        this.closePoker.node.stopAllActions();
        this.closePoker.node.position = cc.v3(-81.2, 40);
        this.closePoker.node.scale = 0.59;
        this.closePoker.node.zIndex = 2;
        this.closePoker.showBack();

        this.openPoker.node.stopAllActions();
        this.openPoker.node.position = cc.v3(81, 30, 0);
        this.openPoker.node.eulerAngles = cc.v3(0, 0, 0);
        this.openPoker.node.scale = 0.59;
        this.openPoker.node.zIndex = 4;

        this.hide();
    }

    /**
     * 
     */
    public show(): void {
        this.closeBg.active = true;
        this.oepnBg.active = true;
        this.wildShow.active = true;
        this.pokerPanel.active = true;
    }

    /**
     * 
     */
    public hide(): void {
        this.setTouchTipActive(false);

        this.pubCacdN.active = false;
        this.light.active = false;
        this.pubCacdNull.active = true;
        this.pubPokerBig.showBack();

        this.wildShow.active = false;
        this.wildPoker1.showBack();
        this.wildPoker2.showBack();
        this.wildShowNull1.active = true;
        this.wildShowNull2.active = true;

        this.pokerPanel.active = false;
        // this.closeBg.active = false;
        // this.oepnBg.active = false;
        this.pubPoker.hide();
        this.closePoker.hide();
        this.openPoker.hide();
    }

    /**
     * 
     * @param cardValue
     */
    public setWildPoker(cardValue: number[]): void {
        this.wildPoker1.initCard(cardValue[0], true);
        this.wildPoker2.initCard(cardValue[1], true);
        this.wildShowNull1.active = false;
        this.wildShowNull2.active = false;
    }

    /**
     * 
     * @param cardValue
     */
    public setPubPoker(cardValue: number): void {
        this.pubPoker.show();
        this.pubPoker.initCard(cardValue);
        this.pubPoker.node.stopAllActions();
        this.pubPoker.node.position = cc.v3(-32, 30, 0);
        this.pubPoker.node.eulerAngles = cc.v3(0, 0, -24);
        this.pubPoker.node.scale = 0.59;
        this.pubPoker.node.zIndex = 0;
    }

    /**
     * 
     * @param cardValue
     */
    public setOpenPoker(cardValue: number): void {
        if (cardValue == 0) {
            this.openPoker.hide();
            return;
        }

        this.openPoker.show();
        this.openPoker.initCard(cardValue, CachetaLogicMgr.isWildCard(cardValue));
    }

    /**
     * 
     * @param value
     */
    public setClosePokerNum(value: number): void {
        this.closePoker.node.active = value > 0;
    }

    /**
     * 
     * @param active
     */
    public setTouchTipActive(active: boolean, isFirstPlay: boolean = false): void {
        this.closeTips.active = active;
        let closeAni = this.closeTips.getComponent(cc.Animation);
        if (active) {
            closeAni.node.y = 2.5;
            closeAni.stop();
            closeAni.play(null, 0);
        } else {
            closeAni.stop();
        }

        this.oepnTips.active = active;
        if (isFirstPlay) this.oepnTips.active = false;

        let openAni = this.oepnTips.getComponent(cc.Animation);
        if (active && !isFirstPlay) {
            openAni.node.y = 2.5;
            openAni.stop();
            openAni.play(null, 0);
        } else {
            openAni.stop();
        }
    }

    /**
     * 
     * @param cardValue
     * @param cbFinished
     */
    public playPubandWildPokerAni(
        cardValue: number,
        wildCValues: number[],
        cbFinished: Function
    ): void {
        CachetaAudioMgr.playCardFlip();

        this.setPubPoker(cardValue);
        this.pubPoker.show();
        this.pubPoker.node.stopAllActions();
        this.pubPoker.node.position = cc.v3(-81.2, 38, 0);
        this.pubPoker.node.eulerAngles = cc.v3(0, 0, 0);

        cc.tween(this.pubPoker.node)
            .delay(0.2 / CachetaLogicMgr.timeScale)
            .to(
                0.5 / CachetaLogicMgr.timeScale,
                { position: cc.v3(-32, 30, 0), eulerAngles: cc.v3(0, 0, -24) },
                { easing: cc.easing.quadOut.name }
            )
            .call(() => {
                cbFinished();
            })
            .start();

        cc.Tween.stopAllByTarget(this.wildShow);
        cc.tween(this.wildShow).call(() => {}).start;

        // this.setWildPoker(wildCValues);
        this.wildShowNull1.active = false;
        this.wildShowNull2.active = false;

        cc.tween(this.wildPoker1.node)
            .delay(0.2 / CachetaLogicMgr.timeScale)
            .to(0.25 / CachetaLogicMgr.timeScale, { eulerAngles: cc.v3(0, -90, 0) })
            .call(() => {
                this.wildPoker1.initCard(wildCValues[0], true);
            })
            .to(0.25 / CachetaLogicMgr.timeScale, { eulerAngles: cc.v3(0, 0, 0) })
            .start();
        cc.tween(this.wildPoker2.node)
            .delay(0.2 / CachetaLogicMgr.timeScale)
            .to(0.25 / CachetaLogicMgr.timeScale, { eulerAngles: cc.v3(0, -90, 0) })
            .call(() => {
                this.wildPoker2.initCard(wildCValues[1], true);
            })
            .to(0.25 / CachetaLogicMgr.timeScale, { eulerAngles: cc.v3(0, 0, 0) })
            .start();
    }

    /**
     * 
     */
    public playEnsurePubPokerAni(cardValue: number, callback: Function) {
        this.pubCacdN.active = true;
        cc.Tween.stopAllByTarget(this.pubPokerBig.node);
        let cardValueArr = [];
        for (let i = 0; i < 24; i++) {
            cardValueArr.push(Math.ceil(Math.random() * 4) * 100 + Math.ceil(Math.random() * 13));
        }
        cardValueArr.push(cardValue);
        let time = 0;
        cc.tween(this.pubPokerBig.node)
            .delay(0.5 / CachetaLogicMgr.timeScale)
            .parallel(
                cc.tween().call(() => {
                    this.pubCacdNull.active = false;
                }),
                cc.tween().repeat(
                    25,
                    cc
                        .tween(this.pubPokerBig.node)
                        .delay(0.1 / CachetaLogicMgr.timeScale)
                        .call(() => {
                            this.pubPokerBig.initCard(cardValueArr[time]);
                            time += 1;
                        })
                )
            )
            .call(() => {
                //  1s
                this.light.active = true;
                // cc.tween(this.light).blink(1, 6).start();
            })
            .delay(1 / CachetaLogicMgr.timeScale)
            .call(() => {
                this.pubCacdN.active = false;
                callback && callback();
            })
            .start();
    }

    /**
     * 
     */
    public getClosePokerPos(): cc.Vec3 {
        return this.closePoker.node.convertToWorldSpaceAR(cc.v3(0, 0));
    }

    /**
     * 
     */
    public getOpenPokerPos(): cc.Vec3 {
        return this.openPoker.node.convertToWorldSpaceAR(cc.v3(0, 0));
    }

    private onTouchClosePoker(): void {
        if (
            CachetaUserMgr.isLocalActive() &&
            CachetaLogicMgr.gameStage == CachetaProto.STAGE.TOUCH
        ) {
            CachetaSocketMgr.touchReq(false);
        }
    }

    private onTouchOpenPoker(): void {
        if (
            CachetaUserMgr.isLocalActive() &&
            CachetaLogicMgr.gameStage == CachetaProto.STAGE.TOUCH
        ) {
            CachetaSocketMgr.touchReq(true);
        }
    }

    private onReplayStateChanged(state: ReplayState): void {
        switch (state) {
            case ReplayState.Play:
                this.closeTips.getComponent(cc.Animation).resume();
                this.oepnTips.getComponent(cc.Animation).resume();
                break;
            case ReplayState.Pause:
                this.closeTips.getComponent(cc.Animation).pause();
                this.oepnTips.getComponent(cc.Animation).pause();
                break;
            default:
                break;
        }
    }

    private getNewPoker(poker, parent) {
        let wildPokerNode = cc.instantiate(poker);
        wildPokerNode.parent = parent;
        return wildPokerNode.getComponent(CachetaPoker);
    }
}
