import { CachetaConst } from "../define/CachetaConst";
import CachetaSkin from "../conf/skin/CachetaSkin";
import { CachetaProto } from "../service/ws/proto/CachetaProto";
import CachetaLanguage from "../conf/lang/CachetaLanguage";

const { ccclass, property } = cc._decorator;

@ccclass
export default class CachetaGroupBar extends cc.Component {
    private groupTag: cc.Sprite = null;
    private pattern: cc.Label = null;

    protected onLoad(): void {
        this.groupTag = this.node.getComponent(cc.Sprite);
        this.pattern = cc.find("pattern", this.node).getComponent(cc.Label);
    }

    public show(): void {
        this.node.active = true;
    }

    public hide(): void {
        this.node.active = false;
    }

    /**
     * 
     * @param cardIndex
     * @param groupIndex
     * @param cardNum
     */
    public setLayout(
        cardIndex: number,
        groupIndex: number,
        cardNum: number,
        pokerSpace: number,
        groupSpace: number
    ): void {
        if (cardIndex == 0) {
            this.node.x = 0;
        } else {
            this.node.x = (cardIndex - 1) * pokerSpace + CachetaConst.PokerWidth - 40;
        }
        this.node.width = pokerSpace * (cardNum - 1) + CachetaConst.PokerWidth - groupSpace;
    }

    /**
     * 
     * @param handCard
     * @param cardGroup
     * @param isSettle
     */
    public setPattern(
        handCard: CachetaProto.Hand,
        cardGroup: CachetaProto.Group,
        idx: number,
        isSettle: boolean = false
    ): void {
        let type = 0;
        switch (cardGroup.Pattern) {
            case CachetaProto.PATTERN.STRAIGHT:
                type = 2;
                break;
            case CachetaProto.PATTERN.SET:
                type = 1;
                break;
            default:
                type = 3;
                break;
        }
        if (type == 3) return;

        let y = !isSettle ? -85.5 : -100;
        let height = !isSettle ? 42 : 5;
        let resPrefix = "group";
        let pattern = !isSettle
            ? CachetaLanguage[`PATTERN_${CachetaProto.PATTERN[cardGroup.Pattern]}`]
            : "";

        this.node.y = y;
        this.node.height = height;
        this.groupTag.spriteFrame = UIDepend.SpriteManager.getSpriteBundle(
            CachetaSkin.Priority[`${resPrefix}_${(idx % 2) + 1}`]
        );
        this.pattern.string = pattern;
    }
}
