const { ccclass, property } = cc._decorator;

@ccclass
export default class CachetaToast extends cc.Component {
    private system: cc.Node = null;
    private systemBg: cc.Sprite = null;
    private systemText: cc.RichText = null;

    private tipsNode: cc.Node = null;
    private tipsBg: cc.Sprite = null;
    private tipsText: cc.RichText = null;
    private tipsMargin: number = 0;
    private strText: string = "";
    private leftTime: number = 0;

    protected onLoad(): void {
        this.system = cc.find("system", this.node);
        // this.systemBg = cc.find("bg", this.system).getComponent(cc.Sprite);
        this.systemText = cc.find("text", this.system).getComponent(cc.RichText);

        this.reset();
    }

    public reset(): void {
        this.hide();
    }

    public hide(): void {
        this.node.active = false;
        this.system.active = false;
    }

    /**
     * 
     * @param text
     * @param duration
     * @param posY
     */
    public setSystemTips(text: string, duration: number = -1, posY: number = 0): void {
        this.tipsNode = this.system;
        // this.tipsBg = this.systemBg;
        this.tipsText = this.systemText;
        this.tipsMargin = 200;
        this.setText(text, duration, posY);
    }

    private setText(text: string, duration: number = -1, posY: number): void {
        this.node.active = true;
        this.system.active = false;

        this.tipsNode.active = true;
        this.tipsNode.y = posY;
        this.tipsNode.opacity = 0;
        this.strText = text;
        this.leftTime = duration == -1 ? 65535 : Math.floor(duration) + 1;
        this.unschedule(this.updateText);
        this.schedule(this.updateText, 1);
        this.updateText();
        this.scheduleOnce(() => {
            // let textWidth = this.tipsText.node.width;
            // let width = textWidth > 400 ? textWidth : 400;
            // this.tipsBg.node.width = width + this.tipsMargin;
            this.tipsNode.opacity = 255;
        });
    }

    private updateText(): void {
        this.leftTime -= 1;
        if (this.leftTime < 1) {
            this.hide();
            return;
        }

        if (this.tipsText) {
            this.tipsText.string = this.strText.format(this.leftTime.toString());
        }
    }
}
