export default class CachetaCardModel {
    /**  */
    public cardValue: number;
    /**  */
    public cardIndex: number;
    /**  */
    public curCardIndex: number;
    /**  */
    public groupIndex: number;

    constructor(cardValue: number, cardIndex: number, curCardIndex: number, groupIndex: number) {
        this.cardValue = cardValue;
        this.cardIndex = cardIndex;
        this.curCardIndex = curCardIndex;
        this.groupIndex = groupIndex;
    }

    /**
     * 
     * @param model
     */
    public equal(model: CachetaCardModel): boolean {
        return (
            this.cardValue == model.cardValue &&
            this.cardIndex == model.cardIndex &&
            this.groupIndex == model.groupIndex &&
            this.curCardIndex == model.curCardIndex
        );
    }
}
