import Listener from "../../../../../main/core/utils/Listener";
import { ReplayState } from "../../../../plaza/common/Script/game/replay/GameReplayControl";
import { CachetaEventName } from "../define/CachetaEventName";
import CachetaLogicMgr from "../manager/CachetaLogicMgr";

const { ccclass, property } = cc._decorator;

@ccclass
export default class CachetaUserCounddownProgress extends cc.ProgressBar {
    private time: number = null;
    private curTime: number = 0;
    private isUpdate: boolean = false;

    protected onLoad(): void {
        Listener.on(CachetaEventName.REPLAY_STATE_CHANGED, this.onReplayStateChanged, this);
    }

    protected onDestroy(): void {
        Listener.off(CachetaEventName.REPLAY_STATE_CHANGED, this.onReplayStateChanged, this);
    }

    public play(time: number) {
        this.time = time;
        this.curTime = 0;
        this.isUpdate = true;
    }

    stop() {
        this.progress = 0;
        this.time = 0;
        this.node.children.forEach((node) => {
            node.active = !!this.progress;
            node.angle = this.progress * 360;
        });
        this.isUpdate = false;
    }

    protected update(dt: number) {
        if (!this.isUpdate) return;

        super.update?.(dt);
        this.curTime += dt * CachetaLogicMgr.timeScale;
        if (this.time == 0) {
            return;
        }
        this.progress = (this.time - this.curTime) / this.time;
        if (this.progress <= 0) {
            this.time = 0;
            this.progress = 0;
        }
        this.node.children.forEach((node) => {
            node.active = !!this.progress;
            node.angle = this.progress * 360;
        });
    }

    private onReplayStateChanged(state: ReplayState): void {
        switch (state) {
            case ReplayState.Play:
                this.isUpdate = true;
                break;
            case ReplayState.Pause:
                this.isUpdate = false;
                break;
            default:
                break;
        }
    }
}
