import CachetaSkin from "../conf/skin/CachetaSkin";
import CachetaHandPoker from "./CachetaHandPoker";
import { CachetaProto } from "../service/ws/proto/CachetaProto";
import UserAvatarMgr from "../../../../plaza/common/Script/Component/UserAvatar/UserAvatarMgr";
import CachetaUserMgr from "../manager/CachetaUserMgr";
import { CurrencyUtil } from "../../../../plaza/common/Script/utils/CurrencyUtil";
import UserAvatarModel from "../../../../plaza/common/Script/Component/UserAvatar/UserAvatarModel";
import UserAvatar from "../../../../plaza/common/Script/Component/UserAvatar/UserAvatar";
import CachetaLogicMgr from "../manager/CachetaLogicMgr";
import CachetaPoker from "./CachetaPoker";

const { ccclass, property } = cc._decorator;

@ccclass
export default class CachetaSettlePlayer extends cc.Component {
    private bg: cc.Node;
    private avatar: cc.Node;
    private iconMe: cc.Node;
    private nickname: cc.Label;
    private cards: cc.Node;
    private amount: cc.Label;

    protected onLoad(): void {
        this.avatar = cc.find("head/mask/avatar", this.node);
        // this.iconMe = cc.find("iconMe", this.node);
        this.nickname = cc.find("nickname", this.node).getComponent(cc.Label);
        this.cards = cc.find("cards", this.node);
        this.amount = cc.find("amount", this.node).getComponent(cc.Label);
    }

    public init(settle: CachetaProto.Settle): void {
        if (settle.UserID == CommonDepend.UserManager.user_id) {
            this.setAvatar(CommonDepend.UserManager.avatar, CommonDepend.UserManager.gender);
        } else {
            this.setAvatar(settle.Avatar, settle.Gender);
        }
        // this.setIconMe(settle.UserID);
        this.setNickname(settle.UserName, settle.UserID);
        this.setCards(settle.Hand);
        this.setAmount(settle.Win);
    }

    private setAvatar(avatar: number, gender: UserAvatarModel.GENDER): void {
        UserAvatarMgr.loadUserAvatarPrefab(this.avatar, avatar, gender).then((avatar: cc.Node) => {
            if (cc.isValid(avatar)) {
                avatar.position = cc.v3(0, 0);
                avatar.scaleX = (this.avatar.width + 15) / avatar.width;
                avatar.scaleY = (this.avatar.height + 15) / avatar.height;
            }
        });
    }

    private setIconMe(userId: number): void {
        this.iconMe.active = CachetaUserMgr.isLocalUser(userId);
    }

    private setNickname(nickname: string, userId: number): void {
        this.nickname.string = nickname.limtLength(10);
    }

    private setCards(handCard: CachetaProto.Hand): void {
        let pokerPrefab: cc.Prefab = UIDepend.PrefabManager.getPrefabBundle(
            CachetaSkin.Priority.CachetaPoker
        );
        for (let i = 0; i < handCard.GroupS.length; i++) {
            let cardGroup = handCard.GroupS[i];
            for (let j = 0; j < cardGroup.CardS.length; j++) {
                let cardValue = cardGroup.CardS[j];
                let isWildCard = CachetaLogicMgr.isWildCard(cardValue);
                let poker = cc.instantiate(pokerPrefab).getComponent(CachetaPoker);

                if (i >= this.cards.childrenCount) break;
                poker.node.parent = this.cards.children[i];
                poker.node.setPosition(cc.v3(0, 0, 0));
                poker.show();
                poker.initCard(cardValue, isWildCard);
            }
        }
    }

    private addition = 0;

    private setAmount(amount: number): void {
        let base = cc.sys.localStorage.getItem("Base");
        if (amount < 0) {
            if (base == 10) {
                amount = -500 * 10;
            } else if (base == 50) {
                amount = -500 * 50;
            }
        } else {
            if (base == 10) {
                amount = 1500 * 10;
            } else if (base == 50) {
                amount = 1500 * 50;
            }
        }

        let sign = amount >= 0 ? "+" : "-";
        let color =
            amount >= 0 ? new cc.Color().fromHEX("#fff500") : new cc.Color().fromHEX("#35d7d1");
        this.amount.node.color = color;
        if (amount > 0) {
            let vip = cc.sys.localStorage.getItem("vip");
            if (vip != null) {
                if (vip == "1") {
                    this.addition = 0.01;
                } else if (vip == "2") {
                    this.addition = 0.03;
                } else if (vip == "3") {
                    this.addition = 0.05;
                } else if (vip == "4") {
                    this.addition = 0.08;
                } else if (vip == "5") {
                    this.addition = 0.1;
                }
            } else {
                this.addition = 0;
            }
        }
        let sum = this.addition * amount + amount;
        this.amount.string = sign + Math.abs(sum);

        if (amount >= 0) {
            this.playWin();
        } else {
            this.playLose();
        }
    }

    private playWin(): void {
        let userAvatar = this.avatar.getComponentInChildren(UserAvatar);
        if (userAvatar) {
            userAvatar.playWinAnimation();
        }
    }

    private playLose(): void {
        let userAvatar = this.avatar.getComponentInChildren(UserAvatar);
        if (userAvatar) {
            userAvatar.playLoseAnimation();
        }
    }
}
