import { CachetaConst } from "../define/CachetaConst";
import { CachetaEventName } from "../define/CachetaEventName";
import CachetaAudioMgr from "../manager/CachetaAudioMgr";
import CachetaSkin from "../conf/skin/CachetaSkin";
import CachetaPoker from "./CachetaPoker";
import CachetaGroupBar from "../ui/CachetaGroupBar";
import CachetaCardModel from "../model/CachetaCardModel";
import CachetaSocketMgr from "../service/ws/CachetaSocketMgr";
import { CachetaProto } from "../service/ws/proto/CachetaProto";
import Listener from "../../../../../main/core/utils/Listener";
import CachetaLogicMgr from "../manager/CachetaLogicMgr";
import CachetaUserMgr from "../manager/CachetaUserMgr";

const { ccclass, property } = cc._decorator;

@ccclass
export default class CachetaHandPoker extends cc.Component {
    private panle: cc.Node = null;

    /**  */
    private pokers: CachetaPoker[] = [];
    /**  */
    private groupBars: CachetaGroupBar[] = [];
    /**  */
    private handCard: CachetaProto.Hand = null;
    /**  */
    private models: CachetaCardModel[] = null;
    /** X */
    private pokerXs: number[] = [];
    /** ()X */
    private groupXs: number[] = [];
    /**  */
    private moveHandCard: CachetaProto.Hand = null;
    /**  */
    private discardPoker: CachetaPoker = null;
    /**  */
    private isFlowerSort: boolean = true;
    /** - */
    private handCardValues: number[] = [];
    /**  */
    private canDecCardV: number = 0;

    private insetCardIndex = -1;

    protected onLoad(): void {
        this.panle = this.node.getChildByName("panle");
        this.panle.destroyAllChildren();

        this.isFlowerSort = true;

        let pokerPrefab: cc.Prefab = UIDepend.PrefabManager.getPrefabBundle(
            CachetaSkin.Priority.CachetaPoker
        );
        for (let i = 0; i < CachetaConst.HandPokerNumMax; i++) {
            let pokerNode = cc.instantiate(pokerPrefab);
            pokerNode.parent = this.panle;

            let poker = pokerNode.getComponent(CachetaPoker);
            this.pokers.push(poker);
        }

        let groupPrefab: cc.Prefab = UIDepend.PrefabManager.getPrefabBundle(
            CachetaSkin.Priority.CachetaGroupBar
        );
        for (let i = 0; i < CachetaConst.HandPokerGroupMax; i++) {
            let groupNode = cc.instantiate(groupPrefab);
            groupNode.parent = this.panle;
            groupNode.zIndex = CachetaConst.HandPokerNumMax + 1 + i;

            let groupBar = groupNode.getComponent(CachetaGroupBar);
            this.groupBars.push(groupBar);
        }

        Listener.on(CachetaEventName.POKER_TOUCH_START, this.onPokerTouchStart, this);
        Listener.on(CachetaEventName.POKER_TOUCH_MOVE, this.onPokerTouchMove, this);
        Listener.on(CachetaEventName.POKER_TOUCH_END, this.onPokerTouchEnd, this);
        Listener.on(CachetaEventName.MANUAL_SORT, this.onManualSort, this);
        Listener.on(CachetaEventName.POKER_STATE_RESET, this.onResetHandPoker, this);

        this.reset();
    }

    protected onDestroy(): void {
        Listener.off(CachetaEventName.POKER_TOUCH_START, this.onPokerTouchStart, this);
        Listener.off(CachetaEventName.POKER_TOUCH_MOVE, this.onPokerTouchMove, this);
        Listener.off(CachetaEventName.POKER_TOUCH_END, this.onPokerTouchEnd, this);
        Listener.off(CachetaEventName.MANUAL_SORT, this.onManualSort, this);
        Listener.off(CachetaEventName.POKER_STATE_RESET, this.onResetHandPoker, this);
    }

    public reset(): void {
        this.canDecCardV = 0;

        this.node.stopAllActions();

        for (let i = 0; i < this.pokers.length; i++) {
            let poker = this.pokers[i];
            poker.node.stopAllActions();
            poker.node.eulerAngles = cc.v3(0, 0, 0);
            poker.node.scale = 1;
            poker.node.zIndex = i + CachetaConst.HandPokerGroupMax;
            poker.hide();
        }

        for (let j = 0; j < this.groupBars.length; j++) {
            this.groupBars[j].hide();
        }
    }

    /**
     * 
     * @param handCard
     */
    public setPokerValue(handCard: CachetaProto.Hand, saveHandCardValue: boolean = false): void {
        this.handCard = handCard;
        if (saveHandCardValue) {
            this.handCardValues = [];
        }
        this.models = [];
        this.pokerXs = [];
        this.groupXs = [];

        for (let i = 0; i < this.pokers.length; i++) {
            this.pokers[i].reset();
        }

        let cardIndex = 0;
        let cardZIndex = 0;
        for (let i = 0; i < handCard.GroupS.length; i++) {
            let cardGroup = handCard.GroupS[i];
            for (let j = 0; j < cardGroup.CardS.length; j++) {
                let cardValue = cardGroup.CardS[j];
                let isWildCard = CachetaLogicMgr.isWildCard(cardValue);

                //   cardIndex curCardIndex
                let fromIndex = 0;
                let model = new CachetaCardModel(
                    cardValue,
                    saveHandCardValue
                        ? cardIndex
                        : this.handCardValues.indexOf(cardValue, fromIndex),
                    cardIndex,
                    i
                );
                // let pokerX = cardIndex * CachetaConst.PokerSpace + i * CachetaConst.GroupSpace;
                let pokerX = cardIndex * CachetaConst.PokerSpace + CachetaConst.PokerWidth / 2;
                let poker = this.pokers[cardIndex];
                poker.node.zIndex = cardZIndex;
                poker.show();
                poker.initCard(cardValue, isWildCard);
                poker.setModel(model);
                poker.setPositionX(pokerX);
                poker.setTouchEnabled(true);

                this.models.push(model);
                this.pokerXs.push(pokerX);

                cardIndex++;
                cardZIndex++;
                if (
                    j == cardGroup.CardS.length - 1 &&
                    cardGroup.Pattern != CachetaProto.PATTERN.INVALID
                ) {
                    cardZIndex++;
                }
                if (saveHandCardValue) {
                    this.handCardValues.push(cardValue);
                }
            }

            this.groupXs[i] =
                (cardIndex - 1) * CachetaConst.PokerSpace +
                i * CachetaConst.GroupSpace +
                CachetaConst.PokerWidth / 2;
        }

        CC_DEBUG && cc.log(`CachetaHandPoker handCardValues: ${this.handCardValues}`);

        if (saveHandCardValue) {
            this.canDecCardV = 0;
            if (
                handCard.Status == CachetaProto.HAND_STATUS.LISTEN &&
                cardIndex == CachetaConst.HandPokerNumMax
            ) {
                handCard.GroupS.forEach((group) => {
                    if (group.Pattern == CachetaProto.PATTERN.INVALID && group.CardS.length == 1) {
                        this.canDecCardV = group.CardS[0];
                    }
                });
            }
        }

        // 
        for (let i = cardIndex; i < this.pokers.length; i++) {
            this.pokers[i].setPositionX(
                // i * CachetaConst.PokerSpace + (handCard.GroupS.length - 1) * CachetaConst.GroupSpace
                cardIndex * CachetaConst.PokerSpace + CachetaConst.PokerWidth / 2
            );
        }

        // 
        let handWidth =
            (CachetaLogicMgr.getHandCardsNum(this.handCard) - 1) * CachetaConst.PokerSpace;
        this.panle.x = -(handWidth + CachetaConst.PokerWidth) / 2;

        this.setGroupBar(handCard);
    }

    /**
     * 
     * @param handCard 
     */
    private setGroupBar(handCard: CachetaProto.Hand): void {
        for (let i = 0; i < this.groupBars.length; i++) {
            this.groupBars[i].hide();
        }

        let groupIdx = 0;
        for (let i = 0, cardIndex = 0; i < handCard.GroupS.length; i++) {
            let cardGroup = handCard.GroupS[i];
            if (cardGroup.CardS.length > 1 && cardGroup.Pattern != CachetaProto.PATTERN.INVALID) {
                let groupBar: CachetaGroupBar = this.groupBars[i];
                groupIdx += cardGroup.CardS.length;
                groupBar.node.zIndex = groupIdx;
                groupIdx++;
                groupBar.show();
                groupBar.setLayout(
                    cardIndex,
                    i,
                    cardGroup.CardS.length,
                    CachetaConst.PokerSpace,
                    CachetaConst.GroupSpace
                );
                groupBar.setPattern(handCard, cardGroup, i);
            } else {
                groupIdx += cardGroup.CardS.length;
            }
            cardIndex = cardIndex + cardGroup.CardS.length;
        }
    }

    /**
     * 
     * @param poker
     */
    private onPokerTouchStart(poker: CachetaPoker): void {
        this.moveHandCard = null;

        for (let i = 0; i < this.pokers.length; i++) {
            if (this.pokers[i] != poker) {
                this.pokers[i].setTouchEnabled(false);
            }
        }
    }

    /**
     * 
     * @param poker
     */
    private onPokerTouchMove(poker: CachetaPoker): void {
        poker.node.zIndex = CachetaConst.HandPokerNumMax + CachetaConst.HandPokerGroupMax;

        let position = poker.node.position;
        if (position.y > poker.node.height) {
            return;
        }

        let insetCardIndex = this.getInsetCardIndex(poker);
        let insetGroupIndex = this.getInsetGroupIndex(poker);
        let oldModel = new CachetaCardModel(
            poker.getCardValue(),
            poker.getCardIndex(),
            poker.getCurCardIndex(),
            poker.getGroupIndex()
        );
        let newModel = new CachetaCardModel(
            poker.getCardValue(),
            poker.getCardIndex(),
            insetCardIndex,
            insetGroupIndex
        );

        this.moveHandCard = this.getMoveHandCard(oldModel, newModel);
        // this.setGroupBar(this.moveHandCard);
        this.setPokerMovePos(insetCardIndex);
        this.insetCardIndex = insetCardIndex;
    }

    /**
     * 
     * @param poker
     */
    private onPokerTouchEnd(poker: CachetaPoker): void {
        let position = poker.node.position;
        if (position.y >= 330 - CachetaConst.PokerHeight / 2) {
            if (
                CachetaUserMgr.isLocalActive() &&
                CachetaLogicMgr.gameStage == CachetaProto.STAGE.DISCARD
            ) {
                this.setDiscardPoker(poker);
                if (this.canDecCardV == poker.getCardValue()) {
                    CachetaSocketMgr.declareReq(poker.getCardValue(), poker.getCardIndex());
                } else {
                    CachetaSocketMgr.discardReq(poker.getCardValue(), poker.getCardIndex());
                }
                this.setTouchEnabled(false);
                return;
            }
        }

        if (this.moveHandCard) {
            let oldHandCard = this.handCard;
            let newHandCard = this.moveHandCard;

            // 
            for (let i = 0; i < newHandCard.GroupS.length; i++) {
                if (newHandCard.GroupS[i].CardS.length == 0) {
                    newHandCard.GroupS.splice(i, 1);
                    i--;
                }
            }

            // todo 
            if (this.checkPokersCount(oldHandCard) == this.checkPokersCount(newHandCard)) {
                CachetaSocketMgr.groupReq(newHandCard);
                this.setTouchEnabled(false);
                //  
                this.movePokerNode(poker);
            } else {
                CC_DEBUG &&
                    cc.error(
                        `CachetaHandPoker onPokerTouchEnd, handCard error \nold: `,
                        oldHandCard,
                        `\nnew: `,
                        newHandCard
                    );
                this.setPokerValue(oldHandCard);
            }
            return;
        }

        this.setTouchEnabled(true);
    }

    private movePokerNode(poker: CachetaPoker) {
        if (this.insetCardIndex == -1) return;

        this.pokers.splice(poker.getCurCardIndex(), 1);
        this.pokers.splice(this.insetCardIndex, 0, poker);
        this.insetCardIndex = -1;
    }

    /**
     * 
     * @param poker
     */
    private getInsetCardIndex(poker: CachetaPoker): number {
        let cardIndex = poker.getCardIndex();
        let x = poker.node.position.x;
        for (let i = 0; i < this.pokerXs.length - 1; i++) {
            if (i >= cardIndex) {
                if (this.pokerXs[i + 1] - x > 0) {
                    return i;
                }
            } else {
                if (this.pokerXs[i] - x > 0) {
                    return i;
                }
            }
        }

        return this.pokerXs.length - 1;
    }

    /**
     * 
     * @param poker
     */
    private getInsetGroupIndex(poker: CachetaPoker): number {
        let x = poker.node.position.x;
        for (let i = 0; i < this.groupXs.length; i++) {
            //  ing
            if (this.groupXs[i] + CachetaConst.PokerSpace - CachetaConst.PokerSpace / 2 - x > 0) {
                return i;
            }
        }

        return this.groupXs.length - 1;
    }

    /**
     * 
     * @param oldModel
     * @param newModel
     */
    private getMoveHandCard(
        oldModel: CachetaCardModel,
        newModel: CachetaCardModel
    ): CachetaProto.Hand {
        let newModels = this.models.concat([]);
        if (!oldModel.equal(newModel)) {
            newModels.splice(oldModel.curCardIndex, 1);

            // 
            for (let i = 0; i < newModels.length; i++) {
                let model = newModels[i];
                if (oldModel.curCardIndex > newModel.curCardIndex) {
                    // <-
                    if (
                        model.curCardIndex >= newModel.curCardIndex &&
                        model.curCardIndex < oldModel.curCardIndex
                    ) {
                        model.curCardIndex = model.curCardIndex + 1;
                    }
                } else if (newModel.curCardIndex > oldModel.curCardIndex) {
                    // ->
                    if (
                        model.curCardIndex > oldModel.curCardIndex &&
                        model.curCardIndex <= newModel.curCardIndex
                    ) {
                        model.curCardIndex = model.curCardIndex - 1;
                    }
                }
            }

            newModels.push(newModel);

            // 
            newModels.sort((a, b) => {
                return a.curCardIndex - b.curCardIndex;
            });
        }

        let oldHandCard = this.handCard;
        let newHandCard = {
            GroupS: [],
            Status: CachetaProto.HAND_STATUS.UNKNOWN,
        } as CachetaProto.Hand;

        let newCardGroupS = {
            Pattern: CachetaProto.PATTERN.INVALID,
            CardS: [],
        } as CachetaProto.Group;
        for (let i = 0; i < oldHandCard.GroupS.length; i++) {
            for (let j = 0; j < newModels.length; j++) {
                let model = newModels[j];
                if (model.groupIndex == i) {
                    newCardGroupS.CardS.push(model.cardValue);
                }
            }
        }
        // 
        newHandCard.GroupS.push(newCardGroupS);

        return newHandCard;
    }

    /**
     * 
     * @param insetCardIndex
     */
    private setPokerMovePos(insetCardIndex: number): void {
        let pokers: CachetaPoker[] = [];
        for (let i = 0; i < this.pokers.length; i++) {
            let poker = this.pokers[i];
            if (poker.isMoving() == false) {
                pokers.push(poker);
            }
        }

        for (let i = 0; i < pokers.length; i++) {
            let poker = pokers[i];
            let cardIndex = i >= insetCardIndex ? i + 1 : i;
            let groupIndex = poker.getGroupIndex();
            poker.setPositionX(cardIndex * CachetaConst.PokerSpace + CachetaConst.PokerWidth / 2);
        }
    }

    /**
     * 
     * @param enabled
     */
    public setTouchEnabled(enabled: boolean): void {
        for (let i = 0; i < this.pokers.length; i++) {
            this.pokers[i].setTouchEnabled(enabled);
        }
    }

    /**
     * 
     * @param pokers
     * @param cardValue
     * @param cardIndex
     * @param groupIndex
     */
    private checkInPokers(
        pokers: CachetaPoker[],
        cardValue: number,
        cardIndex: number,
        groupIndex: number
    ): boolean {
        for (let i = 0; i < pokers.length; i++) {
            if (
                pokers[i].getCardValue() == cardValue &&
                pokers[i].getCardIndex() == cardIndex &&
                pokers[i].getGroupIndex() == groupIndex
            ) {
                return true;
            }
        }
        return false;
    }

    /**
     * 
     * @param indexs
     */
    public selectPokers(indexs: number[]): void {
        if (indexs.length == 0) {
            return;
        }

        for (let i = 0; i < this.pokers.length; i++) {
            this.pokers[i].setSelected(false);
        }

        for (let i = 0; i < indexs.length; i++) {
            this.pokers[indexs[i]]?.setSelected(true);
        }
    }

    /**
     * 
     */
    public getSelectedPokers(): CachetaPoker[] {
        let pokers: CachetaPoker[] = [];
        for (let i = 0; i < this.pokers.length; i++) {
            let poker = this.pokers[i];
            if (poker.isSelected()) {
                pokers.push(poker);
            }
        }

        return pokers;
    }

    /**
     * 
     * @param handCard
     */
    private checkPokersCount(handCard: CachetaProto.Hand): number {
        let count = 0;
        for (let i = 0; i < handCard.GroupS.length; i++) {
            for (let j = 0; j < handCard.GroupS[i].CardS.length; j++) {
                count++;
            }
        }

        return count;
    }

    /**
     * 
     */
    public playDealAni(handCard: CachetaProto.Hand, startPos: cc.Vec3, cbFinished: Function): void {
        let handWidth =
            (CachetaConst.HandPokerNumMax - 1 - 1) * CachetaConst.PokerSpace +
            CachetaConst.PokerWidth;
        this.panle.x = -handWidth / 2;

        startPos = this.panle.convertToNodeSpaceAR(startPos);

        let cardIndex = 0;
        let count = 0;
        let valueArr = [];
        if (handCard) {
            let handCardD = this.getDisorderHandCard(handCard);

            CachetaAudioMgr.playCardDeal();

            for (let i = 0; i < this.pokers.length; i++) {
                this.pokers[i].reset();
            }

            for (let i = 0; i < handCardD.GroupS.length; i++) {
                let cardGroup = handCardD.GroupS[i];
                for (let j = 0; j < cardGroup.CardS.length; j++) {
                    let cardValue = cardGroup.CardS[j];
                    valueArr.push(cardValue);
                    let poker = this.pokers[cardIndex];
                    poker.node.zIndex = 0;
                    poker.show();
                    poker.showBack();
                    poker.setTouchEnabled(false);

                    poker.node.stopAllActions();
                    poker.node.scale = 0.59;
                    poker.node.position = startPos;

                    cc.tween(poker.node)
                        .call(() => {
                            poker.showBack();
                        })
                        .delay((0.5 / 9 / CachetaLogicMgr.timeScale) * cardIndex)
                        .to(0.2 / CachetaLogicMgr.timeScale, {
                            scale: 1,
                            position: cc.v3(
                                cardIndex * CachetaConst.PokerSpace + CachetaConst.PokerWidth / 2,
                                0
                            ),
                        })
                        .call(() => {
                            CachetaAudioMgr.playCardIn();
                            count++;
                            if (count == CachetaConst.HandPokerNumMax - 1) {
                                // cbFinished();

                                for (let k = 0; k < cardIndex; k++) {
                                    let poker = this.pokers[k];
                                    poker.node.stopAllActions();

                                    cc.tween(poker.node)
                                        .delay((0.3 / 9 / CachetaLogicMgr.timeScale) * k)
                                        .to(0.1 / CachetaLogicMgr.timeScale, {
                                            eulerAngles: cc.v3(0, -90, 0),
                                        })
                                        .call(() => {
                                            poker.initCard(
                                                valueArr[k],
                                                false //CachetaLogicMgr.isWildCard(valueArr[k])
                                            );
                                        })
                                        .to(0.1 / CachetaLogicMgr.timeScale, {
                                            eulerAngles: cc.v3(0, 0, 0),
                                        })
                                        .call(() => {
                                            if (k == cardIndex - 1) {
                                                cbFinished();
                                            }
                                        })
                                        .start();
                                }
                            }
                        })
                        .start();

                    cardIndex++;
                }
            }
        } else {
            this.node.stopAllActions();
            cc.tween(this.node)
                .delay(0.5 / CachetaLogicMgr.timeScale)
                .call(() => {
                    cbFinished();
                })
                .start();
        }
    }

    /**
     * 
     * @param handCard
     */
    private getDisorderHandCard(handCard: CachetaProto.Hand): CachetaProto.Hand {
        if (handCard == null) {
            return null;
        }

        let oldHandCard = handCard;
        let newHandCard = {
            GroupS: [],
            Status: oldHandCard.Status,
        } as CachetaProto.Hand;

        let newCardGroupS = {
            Pattern: CachetaProto.PATTERN.INVALID,
            CardS: [],
        } as CachetaProto.Group;

        for (let i = 0; i < oldHandCard.GroupS.length; i++) {
            for (let j = 0; j < oldHandCard.GroupS[i].CardS.length; j++) {
                newCardGroupS.CardS.push(oldHandCard.GroupS[i].CardS[j]);
            }
        }

        let cards = newCardGroupS.CardS;
        let count = cards.length;
        let lastIndex = cards.length - 1;
        let randomIndex = 0;
        let temp = 0;

        while (count > 0) {
            // 
            randomIndex = Math.floor(Math.random() * count);
            temp = cards[randomIndex];
            cards[randomIndex] = cards[lastIndex];
            cards[lastIndex] = temp;
            count--;
        }

        newHandCard.GroupS.push(newCardGroupS);
        return newHandCard;
    }

    /**
     * 
     */
    public getTouchPokerPos(): cc.Vec3 {
        let pos = this.pokers[this.pokers.length - 1].node.position;
        return this.panle.convertToWorldSpaceAR(pos);
    }

    /**
     * 
     * @returns
     */
    public setTouchPokerValue(cardValue: number): void {
        this.pokers[this.pokers.length - 1].show();
        this.pokers[this.pokers.length - 1].hideCardFlower();
        this.pokers[this.pokers.length - 1].initCard(cardValue);
    }

    /**
     * 
     * @param discardPoker
     */
    public setDiscardPoker(discardPoker: CachetaPoker): void {
        this.discardPoker = discardPoker;
    }

    /**
     * 
     */
    public getDiscardPokerPos(): cc.Vec3 {
        let pos: cc.Vec3;
        if (this.discardPoker) {
            pos = this.discardPoker.node.position;
            this.discardPoker = null;
        } else {
            pos = this.pokers[this.pokers.length - 1].node.position;
        }

        return this.panle.convertToWorldSpaceAR(pos);
    }

    /**
     * 
     * @param pos
     */
    public setPanlePos(pos: cc.Vec3): void {
        this.panle.position = pos;
    }

    /**
     * 
     * @param handCard
     */
    public setPokerValueBySettle(handCard: CachetaProto.Hand): void {
        let pokerSpace = 65;
        let groupSpace = 100;
        for (let i = 0, cardIndex = 0; i < handCard.GroupS.length; i++) {
            let cardGroup = handCard.GroupS[i];
            if (cardGroup.CardS.length > 1) {
                let groupBar: CachetaGroupBar = this.groupBars[i];
                groupBar.show();
                groupBar.setLayout(cardIndex, i, cardGroup.CardS.length, pokerSpace, groupSpace);
                groupBar.setPattern(handCard, cardGroup, i, true);
            }

            for (let j = 0; j < cardGroup.CardS.length; j++) {
                let cardValue = cardGroup.CardS[j];
                let isWildCard = CachetaLogicMgr.isWildCard(cardValue);
                let poker = this.pokers[cardIndex];
                poker.node.zIndex = cardIndex;
                poker.show();
                poker.initCard(cardValue, isWildCard);
                poker.setPositionX(cardIndex * pokerSpace + i * groupSpace);
                cardIndex++;
            }
        }
    }

    /**
     * 
     */
    public onManualSort(): void {
        this.isFlowerSort = !this.isFlowerSort;
        for (let i = 0; i < this.handCard.GroupS.length; i++) {
            let cardGroup = this.handCard.GroupS[i];
            if (cardGroup.Pattern != CachetaProto.PATTERN.INVALID) continue;
            cardGroup.CardS.sort((cardA, cardB) => {
                let numberA = cardA % 100;
                let numberB = cardB % 100;
                let flowerA = Math.floor(cardA / 100); // hei, hong, mei, fang
                flowerA = flowerA == 2 ? 0 : flowerA;
                let flowerB = Math.floor(cardB / 100); // hei, hong, mei, fang
                flowerB = flowerB == 2 ? 0 : flowerB;
                if (this.isFlowerSort) {
                    return flowerA == flowerB ? numberA - numberB : flowerA - flowerB;
                } else {
                    return numberA == numberB ? flowerA - flowerB : numberA - numberB;
                }
            });
        }
        CachetaSocketMgr.groupReq(this.handCard);
    }

    /**
     * 
     * @param pos
     */
    public onResetHandPoker(): void {
        this.getSelectedPokers().forEach((poker) => {
            poker.setSelected(false);
        });
    }
}
