import CachetaSkin from "../conf/skin/CachetaSkin";

export default class CachetaAudioMgr {
    public static playBGM(): void {
        CommonDepend.AudioManager.playMusic(CachetaSkin.LoadOnDemand.bgm);
    }

    public static playEffect(firesDesc: IResDescribe): void {
        CommonDepend.AudioManager.playEffect(firesDesc);
    }

    public static playCardClick(): void {
        this.playEffect(CachetaSkin.LoadOnDemand.card_click);
    }

    public static playCardDeal(): void {
        this.playEffect(CachetaSkin.LoadOnDemand.card_deal);
    }

    public static playCardFlip(): void {
        this.playEffect(CachetaSkin.LoadOnDemand.card_flip);
    }

    public static playCardIn(): void {
        this.playEffect(CachetaSkin.LoadOnDemand.card_in);
    }

    public static playCardOut(): void {
        this.playEffect(CachetaSkin.LoadOnDemand.card_out);
    }

    public static playCardSort(): void {
        this.playEffect(CachetaSkin.LoadOnDemand.card_sort);
    }

    public static playClickBtn(): void {
        this.playEffect(CachetaSkin.LoadOnDemand.click_btn);
    }

    public static playCountdown(): void {
        this.playEffect(CachetaSkin.LoadOnDemand.countdown);
    }

    public static playOperation(): void {
        this.playEffect(CachetaSkin.LoadOnDemand.operation);
    }

    public static playLose(): void {
        this.playEffect(CachetaSkin.LoadOnDemand.lose);
    }

    public static playWin(): void {
        this.playEffect(CachetaSkin.LoadOnDemand.win);
    }

    public static playExplode(): void {
        this.playEffect(CachetaSkin.LoadOnDemand.explode);
    }

    public static playDeclare(): void {
        this.playEffect(CachetaSkin.LoadOnDemand.declare);
    }
}
