import Listener from "../../../../main/core/utils/Listener";
import CommonBroadCastUtil from "../../../plaza/common/Script/broad_cast/CommonBroadCastUtil";
import CommonEventName from "../../../plaza/common/Script/CommonEventName";
import { CachetaEventName } from "./define/CachetaEventName";
import CachetaAudioMgr from "./manager/CachetaAudioMgr";
import CachetaLogicMgr from "./manager/CachetaLogicMgr";
import CachetaUIMgr from "./manager/CachetaUIMgr";
import CachetaUserMgr from "./manager/CachetaUserMgr";
import CachetaSocketMgr from "./service/ws/CachetaSocketMgr";

const { ccclass, property } = cc._decorator;

@ccclass
export default class CachetaGame extends cc.Component {
    protected onLoad(): void {
        CachetaUserMgr.init();
        CachetaLogicMgr.init();
        CachetaUIMgr.init(this.node);
        CachetaAudioMgr.playBGM();

        Listener.on(CommonEventName.PAY_SUCCESS, this.onPaySuccess, this);
        Listener.on(CachetaEventName.CANCEL_POKER_SELECT, this.onCancelPokerSelect, this);
    }

    protected onDestroy(): void {
        CachetaSocketMgr.close();
        CommonBroadCastUtil.reset();

        Listener.off(CommonEventName.PAY_SUCCESS, this.onPaySuccess, this);
        Listener.off(CachetaEventName.CANCEL_POKER_SELECT, this.onCancelPokerSelect, this);
    }

    private onPaySuccess(): void {
        CachetaSocketMgr.syncUserReq();
    }

    private onCancelPokerSelect(): void {
        CachetaUIMgr.adaptOperation();
    }
}
