import CachetaPoker from "./CachetaPoker";
import CachetaSkin from "../conf/skin/CachetaSkin";
import { CachetaConst } from "../define/CachetaConst";
import CachetaLogicMgr from "../manager/CachetaLogicMgr";
import CachetaSocketMgr from "../service/ws/CachetaSocketMgr";
import Listener from "../../../../../main/core/utils/Listener";
import { CachetaEventName } from "../define/CachetaEventName";

const { ccclass, property } = cc._decorator;

@ccclass
export default class CachetaAction extends cc.Component {
    /**  */
    private touchPoker: CachetaPoker = null;
    /**  */
    private discardPoker: CachetaPoker = null;
    /**  */
    private dealPokers: CachetaPoker[] = [];
    /**  */
    private block: cc.Node = null;
    private cannonball: cc.Node = null;

    protected onLoad(): void {
        let pokerPrefab: cc.Prefab = UIDepend.PrefabManager.getPrefabBundle(
            CachetaSkin.Priority.CachetaPoker
        );

        let touchPokerNode = cc.instantiate(pokerPrefab);
        touchPokerNode.parent = this.node;
        this.touchPoker = touchPokerNode.getComponent(CachetaPoker);

        let discardPokerNode = cc.instantiate(pokerPrefab);
        discardPokerNode.parent = this.node;
        this.discardPoker = discardPokerNode.getComponent(CachetaPoker);

        // 
        for (let i = 0; i < 2 * (CachetaConst.HandPokerNumMax - 1); i++) {
            let pokerNode = cc.instantiate(pokerPrefab);
            pokerNode.parent = this.node;
            let poker = pokerNode.getComponent(CachetaPoker);
            this.dealPokers.push(poker);
        }

        this.block = cc.find("block", this.node);
        this.block.getComponent(cc.Widget).target = this.node.parent;

        this.cannonball = cc.find("cannonball", this.node);

        this.block.on(cc.Node.EventType.TOUCH_START, this.onTouchBlockStart, this);

        this.reset();

        Listener.listen(CachetaEventName.ROB_FIRE, this.playCannonRunAction, this);
    }

    protected onDestroy(): void {
        Listener.listenOff(CachetaEventName.ROB_FIRE, this.playCannonRunAction, this);
    }

    public reset(): void {
        this.node.stopAllActions();
        let children = this.node.children;
        for (let i = 0; i < children.length; i++) {
            children[i].stopAllActions();
        }

        this.touchPoker.hide();
        this.discardPoker.hide();
        this.dealPokers.forEach((poker) => {
            poker.hide();
        });
        this.setBlockActive(false);
        this.cannonball.active = false;
    }

    public setBlockActive(active: boolean): void {
        this.block.active = active;
    }

    private onTouchBlockStart(event: cc.Event.EventTouch): void {
        CachetaSocketMgr.cancelTrustReq();
    }

    /**
     * 
     * @param startPos
     * @param endPos
     * @param cardValue
     * @param isSelf
     * @param callback
     */
    public playTouchPokerAni(
        startPos: cc.Vec3,
        endPos: cc.Vec3,
        cardValue: number,
        isSelf: boolean,
        isOpen: boolean,
        callback: Function = null
    ): void {
        startPos = this.node.convertToNodeSpaceAR(startPos);
        endPos = this.node.convertToNodeSpaceAR(endPos);

        this.touchPoker.show();

        if (isOpen) {
            this.touchPoker.initCard(cardValue);
        } else {
            this.touchPoker.showBack();
        }

        this.touchPoker.node.stopAllActions();
        this.touchPoker.node.scale = 0.59;
        this.touchPoker.node.position = startPos;

        cc.tween(this.touchPoker.node)
            .parallel(
                cc.tween().to(0.5 / CachetaLogicMgr.timeScale, {
                    scale: isSelf ? 1 : 0.44,
                    position: endPos,
                }),
                cc.tween().call(() => {
                    //   
                    if (isSelf && !isOpen) {
                        cc.tween(this.touchPoker.node)
                            .to(0.25 / CachetaLogicMgr.timeScale, { eulerAngles: cc.v3(0, -90, 0) })
                            .call(() => {
                                this.touchPoker.initCard(cardValue);
                            })
                            .to(0.25 / CachetaLogicMgr.timeScale, { eulerAngles: cc.v3(0, 0, 0) })
                            .start();
                    }
                })
            )
            .call(() => {
                this.touchPoker.hide();
                if (callback) {
                    callback();
                }
            })
            .start();
    }

    /**
     * 
     * @param startPos
     * @param endPos
     * @param cardValue
     * @param isSelf
     * @param callback
     */
    public playDiscardPokerAni(
        startPos: cc.Vec3,
        endPos: cc.Vec3,
        cardValue: number,
        isSelf: boolean,
        callback: Function = null
    ): void {
        startPos = this.node.convertToNodeSpaceAR(startPos);
        endPos = this.node.convertToNodeSpaceAR(endPos);

        // 
        this.touchPoker.hide();
        this.touchPoker.node.stopAllActions();

        this.discardPoker.show();
        this.discardPoker.initCard(cardValue);

        this.discardPoker.node.stopAllActions();
        this.discardPoker.node.scale = isSelf ? 1 : 0.44;
        this.discardPoker.node.position = startPos;

        cc.tween(this.discardPoker.node)
            .to(0.2 / CachetaLogicMgr.timeScale, { scale: 0.59, position: endPos })
            .call(() => {
                this.discardPoker.hide();
                if (callback) {
                    callback();
                }
            })
            .start();
    }

    /**
     * 
     * @param startPos
     * @param endPosList
     * @param cbFinished
     */
    public playOtherDealAni(
        startPos: cc.Vec3,
        endPosList: cc.Vec3[],
        cbFinished: Function = null
    ): void {
        startPos = this.node.convertToNodeSpaceAR(startPos);

        let pokerPrefab: cc.Prefab = UIDepend.PrefabManager.getPrefabBundle(
            CachetaSkin.Priority.CachetaPoker
        );
        let index = 0;
        for (let i = 0; i < endPosList.length; i++) {
            let endPos = this.node.convertToNodeSpaceAR(endPosList[i]);
            for (let j = 0; j < CachetaConst.HandPokerNumMax - 1; j++) {
                let poker = this.dealPokers[index];
                if (!poker) {
                    let pokerNode = cc.instantiate(pokerPrefab);
                    pokerNode.parent = this.node;
                    poker = pokerNode.getComponent(CachetaPoker);
                    this.dealPokers.push(poker);
                }
                index++;

                poker.show();
                poker.showBack();
                poker.node.stopAllActions();
                poker.node.scale = 0.59;
                poker.node.position = startPos;

                cc.tween(poker.node)
                    .delay((0.5 / 9 / CachetaLogicMgr.timeScale) * j)
                    .to(0.2 / CachetaLogicMgr.timeScale, { scale: 0.44, position: endPos })
                    .call(() => {
                        poker.hide();
                        if (i == endPosList.length - 1 && j == CachetaConst.HandPokerNumMax - 2) {
                            cbFinished && cbFinished();
                        }
                    })
                    .start();
            }
        }
    }
    /**
     * 
     */
    public playCannonRunAction(startPos: cc.Vec3, endPos: cc.Vec3, callback) {
        this.cannonball.active = true;
        this.cannonball.setPosition(this.node.convertToNodeSpaceAR(startPos));
        this.cannonball.angle = 0;
        cc.tween(this.cannonball)
            .to(
                0.8 / CachetaLogicMgr.timeScale,
                {
                    position: this.node.convertToNodeSpaceAR(endPos),
                    angle: 719,
                },
                { easing: cc.easeOut.name }
            )
            .call(() => {
                this.cannonball.active = false;
                this.cannonball.setPosition(cc.v3(0, 0, 0));

                callback && callback();
            })
            .start();
    }
}
