import { CachetaConst } from "../define/CachetaConst";
import CachetaSocketMgr from "../service/ws/CachetaSocketMgr";
import { CachetaProto } from "../service/ws/proto/CachetaProto";
import CachetaUtil from "../utils/CachetaUtil";
import CachetaLogicMgr from "./CachetaLogicMgr";

class CachetaUserMgr {
    /** key: chair */
    private users: Map<number, CachetaProto.UserSt> = new Map<number, CachetaProto.UserSt>();
    /** shf  */
    private shfTag: boolean = false;
    /** id */
    private localUserId: number = 0;
    /**   */
    private localChair = -1;
    /**  */
    private activeChair = -1;
    /**  */
    public winChair = -1;

    public init(): void {
        this.shfTag = false;
        this.localUserId = CommonDepend.UserManager.uid;
    }

    public reset(): void {
        this.users.clear();
        this.localChair = -1;
        this.activeChair = -1;
        this.winChair = -1;
    }

    public setUsers(users: { [chair: string]: CachetaProto.UserSt }): void {
        let localUser: CachetaProto.UserSt = null;
        for (let chair in users) {
            let user = users[chair];
            this.users.set(user.Chair, user);

            if (user.UserID == this.localUserId) {
                localUser = user;
            }
        }

        if (!localUser) {
            CC_DEBUG && cc.error(`CachetaUserMgr setUsers, localUser err`);
            return;
        }

        this.localChair = localUser.Chair;
    }

    public userIn(user: CachetaProto.UserSt): void {
        this.users.set(user.Chair, user);

        if (CachetaSocketMgr.isShf && !this.shfTag) {
            this.shfTag = true;
            this.localUserId = user.UserID;
        }
    }

    public userOut(chair: number): void {
        this.users.delete(chair);
    }

    /**
     * 
     * @param chair
     */
    public getSeat(chair: number): number {
        return (chair - this.localChair + CachetaConst.PlayerNumMax) % CachetaConst.PlayerNumMax;
    }

    /**
     * 
     * @param seat
     */
    public getChair(seat: number): number {
        return (seat + this.localChair) % CachetaConst.PlayerNumMax;
    }

    public getLocalChair(): number {
        return this.localChair;
    }

    public isLocalChair(chair: number): boolean {
        return this.localChair == chair;
    }

    public getLocalUser(): CachetaProto.UserSt {
        return this.users.get(this.localChair);
    }

    public isLocalUser(userId: number): boolean {
        return this.localUserId == userId;
    }

    public setLocalHandCard(handCard: CachetaProto.Hand): void {
        if (!handCard) {
            return;
        }

        let localUser = this.getLocalUser();
        localUser.Hand = handCard;
    }

    public getLocalHandCard(): CachetaProto.Hand {
        let localUser = this.getLocalUser();
        return localUser.Hand;
    }

    public setActiveChair(chair: number): void {
        this.activeChair = chair;
    }

    public isLocalActive(): boolean {
        return this.localChair == this.activeChair;
    }

    public getUserByChair(chair: number): CachetaProto.UserSt {
        return this.users.get(chair);
    }

    public getUserByUserId(userId: number): CachetaProto.UserSt {
        for (const iter of this.users.values()) {
            if (iter.UserID == userId) {
                return iter;
            }
        }
        return null;
    }

    public getChairByUserId(userId: number): number {
        for (const iter of this.users.values()) {
            if (iter.UserID == userId) {
                return iter.Chair;
            }
        }
        return 0;
    }

    public getNicknameByChair(chair: number): string {
        let user = this.getUserByChair(chair);
        return user.UserName;
    }

    public getNicknameByUserId(userId: number): string {
        let user = this.getUserByUserId(userId);
        return user.UserName;
    }

    public setCredit(chair: number, amount: number): void {
        let user = this.getUserByChair(chair);
        user.Credit = amount;
    }

    public getCredit(chair: number): number {
        let user = this.getUserByChair(chair);
        return user.Credit;
    }

    public setStatusAll(status: CachetaProto.STATUS): void {
        for (const iter of this.users.values()) {
            iter.Status = status;
        }
    }

    public setStatus(chair: number, status: CachetaProto.STATUS): void {
        let user = this.getUserByChair(chair);
        user.Status = status;
    }

    public getStatus(chair: number): CachetaProto.STATUS {
        let user = this.getUserByChair(chair);
        return user?.Status ?? CachetaProto.STATUS.U_PREPARE;
    }

    public getLocalStatus(): CachetaProto.STATUS {
        let localUser = this.getLocalUser();
        return localUser?.Status ?? CachetaProto.STATUS.U_PREPARE;
    }
}

export default new CachetaUserMgr();
