import { LanguageType } from "../../../../../../main/core/conf/LanguageType";
import BaseBundleLanguage from "../../../../../../main/core/language/BaseBundleLanguage";
import { CachetaLanguageEn } from "./en/CachetaLanguageEn";
import { CachetaLanguagePt } from "./pt/CachetaLanguagePt";
import { CachetaLanguageVi } from "./vi/CachetaLanguageVi";

type TLanguage = typeof CachetaLanguageEn &
    typeof CachetaLanguagePt &
    typeof CachetaLanguageVi;
class CachetaLanguage extends BaseBundleLanguage<TLanguage> {
    protected language: LG.ILanguageConfigs = {
        [LanguageType.EN]: CachetaLanguageEn,
        [LanguageType.PT]: CachetaLanguagePt,
        [LanguageType.VI]: CachetaLanguageVi,
    };
}
export default new CachetaLanguage().get();
