import { CurrencyUtil } from "../../../../plaza/common/Script/utils/CurrencyUtil";
import CommonVersusLanguage from "../../../../plaza/common_versus/Script/conf/lang/CommonVersusLanguage";

const { ccclass, property } = cc._decorator;

@ccclass
export default class CachetaTable extends cc.Component {
    private baseScore: cc.Label = null;

    protected onLoad(): void {
        this.baseScore = cc.find("baseScore", this.node).getComponent(cc.Label);
        // this.baseScore.node.active = false;

        this.reset();
    }

    reset() {}

    /**
     * 
     * @param value
     */
    public setBaseScore(value: number): void {
        this.baseScore.node.active = true;
        this.baseScore.string = CommonVersusLanguage.baseSocre.format(value);
    }
}
