import { CachetaConst } from "../define/CachetaConst";
import { CachetaEventName } from "../define/CachetaEventName";
import CachetaCardModel from "../model/CachetaCardModel";
import Listener from "../../../../../main/core/utils/Listener";
import CachetaAudioMgr from "../manager/CachetaAudioMgr";
import Poker from "../../../../plaza/common/Script/Component/Poker/Poker";
import CachetaLogicMgr from "../manager/CachetaLogicMgr";

/**  */
enum EPokerState {
    down = 0, // 
    up = 1, // 
    move = 2, // 
}

const { ccclass, property } = cc._decorator;

@ccclass
export default class CachetaPoker extends Poker {
    @property(cc.Node)
    private wildAdorn: cc.Node = null;

    /**  */
    private model: CachetaCardModel = new CachetaCardModel(0, 0, 0, 0);
    /**  */
    private pokerState: number = EPokerState.down;
    /**  */
    private selectedTime: number = 0;
    /**  */
    private touchEnabled: boolean = false;
    /**  */
    private touchPosLast: cc.Vec2 = null;
    /**  */
    private moving: boolean = false;

    protected onLoad(): void {
        this.node.on(cc.Node.EventType.TOUCH_START, this.onTouchStart, this);
        this.node.on(cc.Node.EventType.TOUCH_MOVE, this.onTouchMove, this);
        this.node.on(cc.Node.EventType.TOUCH_END, this.onTouchEnd, this);
        this.node.on(cc.Node.EventType.TOUCH_CANCEL, this.onTouchEnd, this);

        this.wildAdorn.active = false;
    }

    public reset(): void {
        this.pokerState = EPokerState.down;
        this.selectedTime = 0;
        this.moving = false;
        this.wildAdorn.active = false;
        this.hide();
    }

    public show(): void {
        this.node.active = true;
    }

    public hide(): void {
        this.node.active = false;
    }

    public async initCard(id: number, isLaiziCard: boolean = false) {
        if (!id) {
            id = 0;
        }

        this.wildAdorn.active = isLaiziCard;

        let number = id % 100;
        let follow = Math.floor(id / 100); // hei, hong, mei, fang
        if (id < 0) {
            this.showCardback();
        } else {
            await this.init(number, follow);
        }
    }

    /**
     * 
     * @param model
     */
    public setModel(model: CachetaCardModel): void {
        this.model = model;
    }

    /**
     * 
     * @param x
     */
    public setPositionX(x: number): void {
        this.node.x = x;
        this.node.y = 0;
        this.pokerState = EPokerState.down;
        this.selectedTime = 0;
    }

    /**
     * 
     * @param touchEnabled
     */
    public setTouchEnabled(touchEnabled: boolean): void {
        this.touchEnabled = touchEnabled;
    }

    private onTouchStart(event: cc.Event.EventTouch): void {
        this.moving = false;

        if (!this.touchEnabled) {
            return;
        }

        this.touchPosLast = event.getLocation();
        Listener.emit(CachetaEventName.POKER_TOUCH_START, this);
    }

    private onTouchMove(event: cc.Event.EventTouch): void {
        if (!this.touchEnabled || !this.touchPosLast) {
            return;
        }

        let touchPosCur = event.getLocation();
        let touchPosSub = touchPosCur.sub(this.touchPosLast);
        let distance = touchPosSub.mag();
        if (distance > 5) {
            this.moving = true;
            this.touchPosLast = touchPosCur;

            let nodePosCur = this.node.getPosition();
            let nodePosNext = nodePosCur.add(touchPosSub);
            this.node.setPosition(nodePosNext);

            Listener.emit(CachetaEventName.POKER_TOUCH_MOVE, this);

            this.setState(EPokerState.move);
        }
    }

    private onTouchEnd(event: cc.Event.EventTouch): void {
        this.moving = false;

        if (!this.touchEnabled) {
            return;
        }

        switch (this.pokerState) {
            case EPokerState.down:
                Listener.emit(CachetaEventName.POKER_STATE_RESET);
                this.setState(EPokerState.up);
                break;
            case EPokerState.up:
                this.setState(EPokerState.down);
                Listener.emit(CachetaEventName.CANCEL_POKER_SELECT);
                break;
            default:
                break;
        }

        Listener.emit(CachetaEventName.POKER_TOUCH_END, this);
        CachetaAudioMgr.playCardClick();
    }

    /**
     * 
     * @param state
     */
    private setState(state: EPokerState): void {
        if (state == this.pokerState) {
            return;
        }

        if (state != EPokerState.move) {
            let y = state == EPokerState.up ? CachetaConst.PokerUpOffsetY : 0;
            cc.tween(this.node)
                .to(0.1 / CachetaLogicMgr.timeScale, { y: y }, { easing: cc.easing.cubicOut.name })
                .start();
            this.selectedTime = state == EPokerState.up ? new Date().getTime() : 0;
        }

        this.pokerState = state;
        state == EPokerState.up && Listener.emit(CachetaEventName.POKER_STATE_CHANGED);
    }

    /**
     * 
     */
    public isMoving(): boolean {
        return this.moving;
    }

    /**
     * 
     * @param selected
     */
    public setSelected(selected: boolean): void {
        this.setState(selected ? EPokerState.up : EPokerState.down);
    }

    /**
     * 
     */
    public isSelected(): boolean {
        return this.pokerState == EPokerState.up;
    }

    /**
     * 
     */
    public getSelectedTime(): number {
        return this.selectedTime;
    }

    /**
     * 
     */
    public getCardValue(): number {
        return this.model.cardValue;
    }

    /**
     * 
     */
    public getCardIndex(): number {
        return this.model.cardIndex;
    }

    /**
     * 
     */
    public getCurCardIndex(): number {
        return this.model.curCardIndex;
    }

    /**
     * 
     */
    public getGroupIndex(): number {
        return this.model.groupIndex;
    }

    /**
     * 
     */
    public getBack() {
        return this.node;
    }

    /**
     * 
     */
    public showBack() {
        this.showCardback();
    }

    /**
     * 
     */
    public hideBack() {}
}
