import UserAvatarMgr from "../../../../plaza/common/Script/Component/UserAvatar/UserAvatarMgr";
import UserAvatarModel from "../../../../plaza/common/Script/Component/UserAvatar/UserAvatarModel";
import CachetaSkin from "../conf/skin/CachetaSkin";
import CachetaLogicMgr from "../manager/CachetaLogicMgr";
import { CachetaProto } from "../service/ws/proto/CachetaProto";
import CachetaPoker from "./CachetaPoker";

const { ccclass, property } = cc._decorator;

@ccclass
export default class CachetaDeclare extends cc.Component {
    @property(cc.Node)
    avatar: cc.Node = null;

    win: cc.Node = null;
    hand: cc.Node = null;
    deathTurn: cc.Node = null;
    panelList: cc.Node[] = [];

    onLoad() {
        this.win = cc.find("win", this.node);
        this.hand = cc.find("hand", this.win);
        this.deathTurn = cc.find("deathTurn", this.node);
        this.hand.children.forEach((node) => {
            this.panelList.push(node);
        });
    }

    init(isMaxRoundSettle: boolean, winSettle: CachetaProto.Settle, callback) {
        if (!isMaxRoundSettle) {
            let pokerPrefab: cc.Prefab = UIDepend.PrefabManager.getPrefabBundle(
                CachetaSkin.Priority.CachetaPoker
            );

            for (let i = 0; i < winSettle.Hand.GroupS.length; i++) {
                let cardGroup = winSettle.Hand.GroupS[i];
                for (let j = 0; j < cardGroup.CardS.length; j++) {
                    let poker = cc.instantiate(pokerPrefab);
                    this.panelList[i].addChild(poker);
                    let pokerJS = poker.getComponent(CachetaPoker);
                    let cardValue = cardGroup.CardS[j];
                    let isWildCard = CachetaLogicMgr.isWildCard(cardValue);
                    pokerJS.initCard(cardValue, isWildCard);
                    poker.opacity = 0;
                }
                let layout = this.panelList[i].getComponent(cc.Layout);
                if (cardGroup.CardS.length > 3) {
                    layout.spacingX = -62;
                }
                layout.updateLayout();
            }

            this.setAvatar(winSettle.Avatar, winSettle.Gender);

            this.scheduleOnce(() => {
                //  delay 1s
                let count = 0;
                for (let i = 0; i < this.panelList.length; i++) {
                    let len = this.panelList[i].childrenCount;

                    this.panelList[i].getComponent(cc.Layout).enabled = false;
                    for (let j = 0; j < len; j++) {
                        this.panelList[i].children[j].active = true;
                        let poker = this.panelList[i].children[j];
                        cc.tween(poker)
                            .to(0, { scale: 0, opacity: 0 })
                            .delay((0.05 * count + 0.2 * i) / CachetaLogicMgr.timeScale)
                            .call(() => {
                                poker.zIndex = 999;
                            })
                            .to(0.3 / CachetaLogicMgr.timeScale, { scale: 1.2, opacity: 255 })
                            .call(() => {
                                poker.zIndex = j;
                            })
                            .to(0.1 / CachetaLogicMgr.timeScale, { scale: 1 })
                            .call(() => {
                                if (j == len - 1) {
                                    this.scheduleOnce(() => {
                                        this.node.destroy();

                                        callback && callback();
                                    }, 2 / CachetaLogicMgr.timeScale);
                                }
                            })
                            .start();
                        count++;
                    }
                }
            }, 1 / CachetaLogicMgr.timeScale);
        } else {
            this.win.active = false;
            cc.tween(this.deathTurn)
                .to(0.3 / CachetaLogicMgr.timeScale, { scale: 1.2 }, { easing: cc.easing.sineOut })
                .to(0.1 / CachetaLogicMgr.timeScale, { scale: 1 }, { easing: cc.easing.sineIn })
                .delay(2 / CachetaLogicMgr.timeScale)
                .to(0.5 / CachetaLogicMgr.timeScale, { opacity: 0 })
                .call(() => {
                    this.node.destroy();

                    callback && callback();
                })
                .start();
        }
    }

    /**
     * 
     * @param avatar
     * @param gender
     */
    public setAvatar(avatar: number, gender: UserAvatarModel.GENDER): void {
        UserAvatarMgr.loadUserAvatarPrefab(this.avatar, avatar, gender).then((avatar: cc.Node) => {
            if (cc.isValid(avatar)) {
                avatar.position = cc.v3(0, 0);
                avatar.scaleX = (this.avatar.width + 15) / avatar.width;
                avatar.scaleY = (this.avatar.height + 15) / avatar.height;
            }
        });
    }
}
